import { apiCall } from "../api";
import { toast } from 'react-toastify';
import { constant } from '../constant/constant'


export const getMilestones = (projectId) => async (dispatch) => {
  try {
    const result = await apiCall({
      url: `${process.env.REACT_APP_API_URL}api/v1/milestones/${projectId}`,
      method: "GET",
    });

    let milestones = result.data.body;
    dispatch({ type: constant.GET_MILESTONES, payload: milestones });
    return result;
  } catch (error) {
    toast.error(error.response.data.message, { autoClose: 2000 });
    return error
  }
}