import React, { useEffect, useState } from 'react';
import DatePicker from "react-datepicker";
import dayjs from "dayjs";
import OutSideClick from './OutSideClick';
import LeftArrow from '../../../assets/images/left-arrow.png';
import RightArrow from '../../../assets/images/right-arrow.png';

import "./React-Datepicker.css";
import styles from './DatePicker.module.scss';

const DatePickerCard = ({ onDateChange, isSingle, defaultDate }) => {

    const [startDate, setStartDate] = useState(new Date());
    const [endDate, setEndDate] = useState(null);
    const [date, setDate] = useState('');
    const [show, setShow] = useState(false)

    useEffect(() => {
        let date = new Date();
        if (defaultDate)
            date = new Date(defaultDate);
        dateChange([date]);
    }, [defaultDate]);

    const BtnAction = type => {
        let date = new Date(type === 1 ? endDate : startDate);
        date.setDate(date.getDate() + (type === 1 ? + 1 : -3));
        dateChange([date])
    }

    const dateChange = (dates) => {
        if (isSingle) {
            let start;
            if (Array.isArray(dates))
                [start] = dates;
            else
                start = dates;
            setDate(dayjs(start).format("DD/MM/YYYY"));
            setStartDate(start);
            setShow(false)
            onDateChange(start);
        } else {
            let [start] = dates;
            const weekNo = new Date(start).getDay();
            let startDt = new Date(start);
            startDt.setDate(startDt.getDate() + (7 - weekNo - 1));
            let endDt = new Date(startDt);
            endDt.setDate(endDt.getDate() - 6);
            setDate(dayjs(endDt).format("DD/MM/YYYY") + ' - ' + dayjs(startDt).format("DD/MM/YYYY"));
            setStartDate(endDt);
            setEndDate(startDt);
            onDateChange(endDt, startDt);
        }
    };

    const multiple = () => {
        return <div className={styles.datepickermainbox}>
            <img className={styles.dateArrow} alt="Left_arrow" onClick={() => { BtnAction(0) }} src={LeftArrow} />
            <input readOnly={true} value={date} onChange={() => { }} onClick={() => { setShow(true) }} type="text" className={styles.dateinput} placeholder="Choose date " />
            {show &&
                <OutSideClick setShow={setShow}>
                    <div className={styles.datepickerbox}>
                        <DatePicker
                            startDate={startDate}
                            className={styles.datepickerinput}
                            selected={startDate}
                            endDate={endDate}
                            dropdownMode="select"
                            inline
                            selectsRange
                            onChange={dateChange}
                        />
                    </div>
                </OutSideClick>
            }
            <img className={styles.dateArrow} src={RightArrow} onClick={() => { BtnAction(1) }} alt="Right_arrow" />
        </div>
    }

    const single = () => {
        return <div className={styles.datepickermainboxSingle}>
            <input readOnly={true} value={date} onChange={() => { }} onClick={() => { setShow(true) }} type="text" className={styles.newDateInput} placeholder="Choose date " />
            {show &&
                <OutSideClick setShow={setShow}>
                    <div className={styles.datepickerbox}>
                        <DatePicker
                            startDate={startDate}
                            className={styles.datepickerinput}
                            selected={startDate}
                            dropdownMode="select"
                            inline
                            onChange={dateChange}
                        />
                    </div>
                </OutSideClick>
            }
        </div>
    }

    return (
        <>
            {!isSingle ? multiple() : single()}

        </>
    )
}

export default DatePickerCard;