import * as constant from "../common/constants/Constants";

export default class ValidatorUtil {
    static fieldValidator(value: any, type: string, isrequired: boolean): string | null {
        if (value !== 0 && !value && isrequired) {
            return constant.requiredMsg;
        } else {
            if (value && type === 'email') {
                const reg = /^([A-Za-z0-9_\-.])+@([A-Za-z0-9_\-.])+\.([A-Za-z]{2,4})$/;
                const inprocessReg = /^[A-Za-z0-9._%+-]+@inprocessgroup.com$/;
                if (reg.test(value) === false) {
                    return constant.invalidEmail;
                }
                else if (inprocessReg.test(value) === false) {
                    return constant.invalidGroupEmail;
                }
                else {
                    return null;
                }
            }
            else if (value && type === 'password') {
                const reg = /^(?!^[0-9]*$)(?!^[a-zA-Z]*$)^([a-zA-Z0-9@\$=!:.#%]{8,16})$/
                if (reg.test(value) === false) {
                    return constant.invalidPassword;
                } else {
                    return null;
                }
            }
            else if (type === 'number') {
                const reg = /^[0-9]*$/
                if (reg.test(value) === false) {
                    return constant.invalidNumber;
                } else {
                    return null;
                }
            }
            else if (type === 'percent') {
                const reg = /^[0-9]*$/
                if (reg.test(value) === false || value > 100) {
                    return constant.invalidNumber;
                } else {
                    return null;
                }
            }
            else if (value.length > 50) {
                return constant.maxLength50;
            }
            else {
                return null;
            }
        }
    }
}