import React, { useEffect, useState } from 'react';
import { ToastContainer } from 'react-toastify';
import PageContainer from '../../common/components/pagecontainer/PageContainer';

import styles from './BimonthlyReport.module.scss';
import { useDispatch, useSelector } from 'react-redux';
import { getAllProjects } from '../../common/core/store/common/commonSelector';
import { getProjects } from '../../common/core/store/common/commonAction';

const BimonthlyReport = () => {
  const dispatch = useDispatch();
  const projects = useSelector(getAllProjects);

  useEffect(() => {
    dispatch(getProjects());
  }, []);

  const getForecastedCurrentPercent = (percent: number, endDate: string) => {
    let totalTimestampDiff = Date.parse(endDate) - Date.now();
    let currentTimestampDiff = Date.parse(new Date(new Date().getFullYear(), new Date().getMonth() + 1, 0).toISOString()) - Date.now();
    if (totalTimestampDiff < currentTimestampDiff) {
      percent = 100;
    } else if (totalTimestampDiff > 0) {
      percent = + percent + (100 - percent) * currentTimestampDiff / totalTimestampDiff;
    } else {
      percent = 0;
    }
    return percent.toFixed(2);
  };

  const getForecastedNextMonthPercent = (percent: number, endDate: string) => {
    let totalTimestampDiff = Date.parse(endDate) - Date.now();
    let nextMonthTimestampDiff = Date.parse(new Date(new Date().getFullYear(), new Date().getMonth() + 2, 0).toISOString()) - Date.now();
    if (totalTimestampDiff < nextMonthTimestampDiff) {
      percent = 100;
    } else if (totalTimestampDiff > 0) {
      percent = + percent + (100 - percent) * nextMonthTimestampDiff / totalTimestampDiff;
    } else {
      percent = 0;
    }
    return percent.toFixed(2);
  };

  const toDateString = (date: string) => {
    return date.split("T")[0];
  }

  return (
    <>
      <PageContainer>
        <div className={styles.timewrapper}>
          <div className={styles.toptitlebox}>
            <div className={styles.leftbox}>
              <p> Your Profile </p>
              <p> <strong>Bimonthly Report </strong>  </p>
            </div>
          </div>
          <div className={styles.mainbox}>
            <div className={styles.mainboxinner}>

              <div className={styles.timesentrywrapper}>
                <div className={styles.entrybox}>
                  <div className={styles.scrollbox}>
                    <div className={styles.entrybox}>
                      <table className={styles.homeTable}>
                        <thead>
                          <tr>
                            <th scope="col">
                              <strong>No </strong>
                            </th>
                            <th scope="col">
                              <strong>Project Name </strong>
                            </th>
                            <th scope="col">
                              <strong>Planned Actual(Baseline) </strong>
                            </th>
                            <th scope="col">
                              <strong>Actual </strong>
                            </th>
                            <th scope="col">
                              <strong>Planned end of the current month(Baseline) </strong>
                            </th>
                            <th scope="col">
                              <strong>Planned end of the current month(Realistic) </strong>
                            </th>
                            <th scope="col">
                              <strong>Forecasted(end next month) - Baseline </strong>
                            </th>
                            <th scope="col">
                              <strong>Forecasted(end next month) - realistic </strong>
                            </th>
                            <th scope="col">
                              <strong>End Date </strong>
                            </th>
                          </tr>
                        </thead>
                        <tbody>
                          {projects && projects.map((_row: any, index: number) => {
                            return [
                              <tr key={index}>
                                <td>{index + 1}</td>
                                <td>{_row.name}</td>
                                <td>{_row.plannedBasedLine}%</td>
                                <td>{_row.realBasedLine}%</td>
                                <td>{getForecastedCurrentPercent(_row.plannedBasedLine, _row.celoxisEndDate)}%</td>
                                <td>{getForecastedCurrentPercent(_row.realBasedLine, _row.celoxisEndDate)}%</td>
                                <td>{getForecastedNextMonthPercent(_row.plannedBasedLine, _row.celoxisEndDate)}%</td>
                                <td>{getForecastedNextMonthPercent(_row.realBasedLine, _row.celoxisEndDate)}%</td>
                                <td>{toDateString(_row.celoxisEndDate)}</td>
                              </tr>
                            ];
                          })}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>



            </div>
          </div>
        </div>
        <ToastContainer />
      </PageContainer>
    </>
  );
}

export default BimonthlyReport;
