import React from "react";
import AddIcon from "@material-ui/icons/Add";
import RemoveIcon from "@material-ui/icons/Remove";
import dayjs from "dayjs";
import Tooltip from "@material-ui/core/Tooltip";

import styles from "./Home.module.scss";

const TimeEntry = ({
  blurHandler,
  getDayCount,
  disableProject,
  unAssigned,
  userProjects,
  dates,
  removeRowFunction,
  addRowFunction,
  chnageHandler,
  tasks,
  project,
  changeProject,
  changeTask,
}) => {
  const getValue = (data, date) => {
    if (data) {
      let index = data.findIndex(
        (ar) => ar.date === dayjs(date).format("YYYY-MM-DD")
      );
      if (index > -1) {
        return data[index].timespent !== "0.0" && data[index].timespent !== "0"
          ? data[index].timespent
          : "";
      }
    }
    return "";
  };

  const timeEntries = (data, ind) => {
    let card = [];
    let startDate = new Date(dates.startDate);

    for (let index = 0; index < 7; index++) {
      card.push(
        <td key={`${index}_td`}>
          <input
            type="text"
            onFocus={(e) => {
              e.target.select();
            }}
            onBlur={blurHandler}
            onChange={chnageHandler}
            disabled={
              data &&
                data[index] &&
                (data[index].isEdit || data[index].isEdit === false)
                ? !data[index].isEdit
                : false
            }
            name=""
            taskid={ind}
            rowid={index}
            value={getValue(data, startDate, ind, index)}
            className={
              data && data[index] && data[index].sheetError
                ? `${styles.errorBorder} ${styles.entryinput}`
                : styles.entryinput
            }
          />
        </td>
      );
      startDate.setDate(startDate.getDate() + 1);
    }
    return card;
  };

  const heading = () => {
    let head = [];
    let startDate = new Date(dates.startDate);
    let month = startDate.getMonth();
    let show = false;
    for (let i = 0; i < 7; i++) {
      head.push(
        <th key={i} scope="col">
          <strong>
            {show && month}
            {"\n"}
          </strong>
          {dayjs(startDate).format("ddd, D")}
        </th>
      );
      startDate.setDate(startDate.getDate() + 1);
    }
    return head;
  };

  const unAssignedItems = (row) => {
    const item = [];
    let startDate = new Date(dates.startDate);
    for (let i = 0; i < 7; i++) {
      // let disabledVal = false;
      // if ((new Date().getDate() > 10 && startDate < firstDay) || startDate < PrevMonthfirstDay)
      //     disabledVal = true;
      item.push(
        <td key={i} data-label="">
          <input
            disabled={
              unAssigned[row] &&
                unAssigned[row].timesheets &&
                unAssigned[row].timesheets[i] &&
                (unAssigned[row].timesheets[i].isEdit ||
                  unAssigned[row].timesheets[i].isEdit === false)
                ? !unAssigned[row].timesheets[i].isEdit
                : false
            }
            type="text"
            name=""
            onFocus={(e) => {
              e.target.select();
            }}
            onBlur={blurHandler}
            value={
              unAssigned[row] &&
                unAssigned[row].timesheets &&
                unAssigned[row].timesheets[i]
                ? unAssigned[row].timesheets[i].timespent !== "0.0" &&
                  unAssigned[row].timesheets[i].timespent !== "0"
                  ? unAssigned[row].timesheets[i].timespent
                  : ""
                : ""
            }
            taskid={row}
            rowid={i}
            onChange={(e) => {
              chnageHandler(e, 1);
            }}
            className={
              unAssigned[row] &&
                unAssigned[row].timesheets &&
                unAssigned[row].timesheets[i] &&
                unAssigned[row].timesheets[i].sheetError
                ? `${styles.errorBorder} ${styles.entryinput}`
                : styles.entryinput
            }
          />
        </td>
      );
      startDate.setDate(startDate.getDate() + 1);
    }
    return item;
  };

  const tooltipDataExtractor = (data, id) => {
    let val = "";
    if (data) {
      let filter = data.filter((o) => o.id === parseInt(id));
      val = filter && filter[0] ? filter[0].name : "";
    }
    return val;
  };

  const taskProjectChecker = (projectId, taskId, currentTaskId, parentTaskId, isSelectable) => {
    let task,
      unAssigne = [];
    if (tasks) task = tasks;
    if (unAssigned) unAssigne = unAssigned;
    let json = task.concat(unAssigne);

    let ar = json.filter((o) => {
      if (
        parseInt(o.projectId) === parseInt(projectId) &&
        parseInt(o.taskId) === parseInt(taskId) &&
        parseInt(o.parentTaskId) === parseInt(parentTaskId) &&
        parseInt(currentTaskId) !== parseInt(taskId)
      ) {
        return o;
      }
    });

    return ar.length > 0 ? false : true;
  };

  const rowHtml = () => {
    let rowHTML = [];
    tasks.forEach((element, row) => {
      let disabledVal = false;
      // if ((new Date().getDate() > 10 && startDate < firstDay) || startDate < PrevMonthfirstDay)
      //     if (new Date(dates.endDate).getMonth() !== new Date().getMonth())
      //         disabledVal = true;
      rowHTML.push(
        <React.Fragment key={row}>
          <tr className={row % 2 === 0 ? styles.evan : styles.odd}>
            <td className={styles.firstlist} data-label="Project name">
              <div className={styles.select}>
                <Tooltip
                  title={tooltipDataExtractor(userProjects, element.projectId)}
                >
                  <select
                    disabled={disableProject}
                    className={
                      element.projectError === true ? styles.errorBorder : ""
                    }
                    onChange={(e) => {
                      changeProject(2, e.target.value, row);
                    }}
                  >
                    <option
                      selected={!element.projectId ? true : false}
                      value={null}
                      hidden={true}
                    >
                      Select Project
                    </option>
                    {userProjects &&
                      userProjects.map((res) => {
                        return (
                          <option
                            selected={res.id === element.projectId}
                            value={res.id}
                            key={res.id}
                          >
                            {res.name}
                          </option>
                        );
                      })}
                  </select>
                </Tooltip>
              </div>
            </td>
            <td className={styles.secondlist} data-label="Task">
              <div className={styles.select}>
                <Tooltip
                  title={tooltipDataExtractor(
                    element.task,
                    element.taskId ? element.taskId : element.id
                  )}
                >
                  <select
                    disabled={disableProject}
                    className={element.taskError && styles.errorBorder}
                    onChange={(e) => {
                      changeTask(2, e.target.value, row);
                    }}
                  >
                    <option selected="selected" hidden="hidden">
                      Select Task
                    </option>
                    {element.task &&
                      element.task.map((res) => {
                        console.log(res, element);
                        return (
                          taskProjectChecker(
                            element.projectId,
                            res.id,
                            element.taskId,
                            element.parentTaskId,
                            res.isSelectable
                          ) && (
                            res.isSelectable ?
                              <option selected={
                                res.id === element.parentTaskId
                              } value={`task~~${res.id}`} type={1}>{res.name}</option> :
                              <optgroup label={res.name}>
                                {res.Tasks &&
                                  res.Tasks.map((task) => (
                                    <option
                                      selected={
                                        task.id === element.taskId
                                      }
                                      type={2}
                                      value={`subtask~~${task.id}`}
                                      key={task.id}
                                    >
                                      {task.name}
                                    </option>
                                  ))}
                              </optgroup>

                          )
                        );
                      })}
                    {element.projectId && !element.task && (
                      <option disabled="disabled"> Loading ... </option>
                    )}
                  </select>
                </Tooltip>
              </div>
            </td>
            {timeEntries(tasks[row] ? tasks[row].timesheets : [], row)}
            <td className={styles.lastlist} data-label="">
              <div className={styles.buttonlabal}>
                {
                  <button
                    onClick={() => {
                      !disabledVal &&
                        removeRowFunction(row, 0, row > 4 ? 0 : 1);
                    }}
                    className={`${styles.themeButton} ${styles.removeButton}`}
                  >
                    {" "}
                    <RemoveIcon />
                  </button>
                }
                <button
                  onClick={() => {
                    !disabledVal && addRowFunction();
                  }}
                  className={styles.themeButton}
                >
                  {" "}
                  <AddIcon />
                </button>
              </div>
            </td>
          </tr>
          <tr className={styles.gapdata}>
            <td colSpan="10"></td>
          </tr>
        </React.Fragment>
      );
    });
    return rowHTML;
  };

  return (
    <>
      {dates && (
        <div className={styles.timesentrywrapper}>
          <div className={styles.entrybox}>
            <div className={styles.scrollbox}>
              <div className={styles.entrybox}>
                <table className={styles.homeTable}>
                  <thead>
                    <tr>
                      <th className={styles.firstlist} scope="col">
                        <strong>Project name </strong>
                      </th>
                      <th className={styles.secondlist} scope="col">
                        {" "}
                        <strong>Task </strong>
                      </th>
                      {heading()}
                      <th className={styles.lastlist} scope="col">
                        &nbsp;
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr className={styles.gapdata}>
                      <td colSpan="10"></td>
                    </tr>
                    {tasks && rowHtml()}
                    <tr className={styles.gapgray}>
                      <td colSpan="10"></td>
                    </tr>
                    <tr className={styles.gapdata}>
                      <td colSpan="10"></td>
                    </tr>
                    {unAssigned &&
                      unAssigned.map((res, row) => {
                        let startDate = new Date(dates.startDate);
                        const date = new Date();
                        const firstDay = new Date(
                          date.getFullYear(),
                          date.getMonth(),
                          1
                        );
                        const PrevMonthfirstDay = new Date(
                          date.getFullYear(),
                          date.getMonth() - 1,
                          1
                        );
                        let disabledVal = false;
                        if (
                          (new Date().getDate() > 10 && startDate < firstDay) ||
                          startDate < PrevMonthfirstDay
                        )
                          if (
                            new Date(dates.endDate).getMonth() !==
                            new Date().getMonth()
                          )
                            disabledVal = true;
                        return (
                          <React.Fragment key={row}>
                            <tr className={styles.bgboxselect}>
                              <td
                                className={styles.firstlist}
                                data-label="Project name"
                              >
                                <div className={styles.select}>
                                  <Tooltip
                                    title={tooltipDataExtractor(
                                      project,
                                      res.projectId
                                    )}
                                  >
                                    <select
                                      disabled={disableProject}
                                      className={
                                        res.projectError === true
                                          ? styles.errorBorder
                                          : ""
                                      }
                                      value={res.projectId}
                                      onChange={(e) => {
                                        changeProject(1, e.target.value, row);
                                      }}
                                    >
                                      <option
                                        selected={!res.projectId ? true : false}
                                        hidden="hidden"
                                        value={null}
                                      >
                                        Select Project
                                      </option>
                                      {project &&
                                        project.map((res) => {
                                          return (
                                            <option
                                              key={res.id}
                                              value={parseInt(res.id)}
                                            >
                                              {res.name}
                                            </option>
                                          );
                                        })}
                                    </select>
                                  </Tooltip>
                                </div>
                              </td>
                              <td
                                className={styles.secondlist}
                                data-label="Task"
                              >
                                <div className={styles.select}>
                                  <Tooltip
                                    title={tooltipDataExtractor(
                                      res.task,
                                      res.taskId ? res.taskId : res.id
                                    )}
                                  >
                                    <select
                                      disabled={disableProject}
                                      className={
                                        res.taskError && styles.errorBorder
                                      }
                                      onChange={(e) => {
                                        changeTask(1, e.target.value, row);
                                      }}
                                    >
                                      <option
                                        selected="selected"
                                        hidden="hidden"
                                      >
                                        Select Task
                                      </option>
                                      {res.task &&
                                        res.task.map((resp) => {
                                          return (
                                            taskProjectChecker(
                                              res.projectId,
                                              resp.id,
                                              res.taskId
                                            ) && (
                                              resp.isSelectable ?
                                                <option selected={
                                                  resp.id === res.parentTaskId
                                                } value={`task~~${resp.id}`} type={1}>{resp.name}</option> :
                                                <optgroup label={resp.name}>
                                                  {resp.Tasks &&
                                                    resp.Tasks.map((task) => (
                                                      <option
                                                        selected={
                                                          task.id === res.taskId
                                                        }
                                                        type={2}
                                                        value={`subtask~~${task.id}`}
                                                        key={task.id}
                                                      >
                                                        {task.name}
                                                      </option>
                                                    ))}
                                                </optgroup>

                                            )
                                          );
                                        })}
                                      {res.projectId && !res.task && (
                                        <option disabled="disabled">
                                          {" "}
                                          Loading ...{" "}
                                        </option>
                                      )}
                                    </select>
                                  </Tooltip>
                                </div>
                              </td>
                              {unAssignedItems(row)}
                              <td className={styles.lastlist} data-label="">
                                <div className={styles.buttonlabal}>
                                  {
                                    <button
                                      onClick={() => {
                                        !disabledVal &&
                                          removeRowFunction(
                                            row,
                                            1,
                                            row > 0 ? 0 : 1
                                          );
                                      }}
                                      className={`${styles.themeButton} ${styles.removeButton}`}
                                    >
                                      {" "}
                                      <RemoveIcon />
                                    </button>
                                  }
                                  <button
                                    onClick={() => {
                                      !disabledVal && addRowFunction(1);
                                    }}
                                    className={styles.themeButton}
                                  >
                                    {" "}
                                    <AddIcon />
                                  </button>
                                </div>
                              </td>
                            </tr>
                            <tr className={styles.gapdata}>
                              <td colSpan="10"></td>
                            </tr>
                          </React.Fragment>
                        );
                      })}
                    <tr className={styles.totalday}>
                      <td className={styles.firstlist} data-label="">
                        Total by day
                      </td>
                      <td className={styles.secondlist} data-label=""></td>
                      <td className={styles.time} data-label="Mon">
                        <strong> {tasks && getDayCount(0)} </strong>
                      </td>
                      <td className={styles.time} data-label="Tue">
                        <strong> {tasks && getDayCount(1)} </strong>
                      </td>
                      <td className={styles.time} data-label="Wed">
                        <strong> {tasks && getDayCount(2)} </strong>
                      </td>
                      <td className={styles.time} data-label="Thu">
                        <strong> {tasks && getDayCount(3)} </strong>
                      </td>
                      <td className={styles.time} data-label="Fri">
                        <strong> {tasks && getDayCount(4)} </strong>
                      </td>
                      <td className={styles.time} data-label="Sat">
                        <strong> {tasks && getDayCount(5)} </strong>
                      </td>
                      <td className={styles.time} data-label="Sun">
                        <strong> {tasks && getDayCount(6)} </strong>
                      </td>
                      <td className={styles.lastlist} data-label="">
                        {" "}
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default TimeEntry;
