import moment from "moment";
import React from "react"
import DatePickerCard from "../../../common/lib/DatePicker/DatePicker";
import styles from '../Admin.module.scss';

interface Props {
    rowsData: any
    deleteTableRows: (index: number) => void
    handleChange: (index: number, e: any) => void
}

const MilestonesTableRows: React.FC<Props> = ({ rowsData, deleteTableRows, handleChange }) => {
    return (
        rowsData.map((data: any, index: number) => {
            const { name, progress, date } = data;
            return (
                <tr key={index}>
                    <td>
                        <input type="text" value={name} onChange={(event) => (handleChange(index, event))} name="name" className="form-control" />
                    </td>
                    <td><input type="text" value={progress} onChange={(event) => (handleChange(index, event))} name="progress" className="form-control" /> </td>
                    <td className={styles.dateField}>
                        <DatePickerCard
                            onDateChange={(event: any) => (handleChange(index, {
                                target: {
                                    value: moment(event).format(),
                                    name: "date",
                                },
                            }))}
                            isSingle={true}
                            defaultDate={date}
                        />
                    </td>
                    <td ><button className={styles.deleteButton} onClick={() => (deleteTableRows(index))}>-</button></td>
                </tr>
            )
        })
    )
}

export default MilestonesTableRows;

