import { constant } from '../constant/constant'

const usersReducer = (state = [], { type, payload } = {}) => {
  switch (type) {    
    case constant.GET_ALL_USER: {
      return payload
    }
    case constant.CREATE_USER:{
      return [ ...state,  payload ];
    }
    case constant.UPDATE_USER:{
      return state.map((c) =>
        c.id === payload.id ? payload : c
      );
    }
    case constant.DELETE_USER:{      
      return state.filter((c) => c.id.toString() !== payload);
    }
    case constant.DELETE_USERS:{
      return state.filter(function (c) {
        return payload.indexOf(c.id) === -1;
        });
    }
    default:
      return state;
  }
};

export default usersReducer;
