import axios from "axios";
import { toast } from "react-toastify";
import StorageUtil from '../../../utils/StorageUtil';

export const apiCall = async ({ url, method, payload }) => {
  try {
    let token = StorageUtil.getLocalStorage("token");
    if (token) {
      axios.defaults.headers.common["access-control-allow-origin"] = "*";
      axios.defaults.headers.common["Authorization"] = "Bearer " + token;
      axios.defaults.headers.common[
        "timezone"
      ] = Intl.DateTimeFormat().resolvedOptions().timeZone;
    }
    const resp = await axios({
      method: method || "get",
      url: url,
      data: payload,
    });
    return resp;
  } catch (error) {
    if (error && error.response && error.response.status === 401 && error.response.data.message.toLowerCase() === 'unauthorized') {
      toast.error("Token expired");
      StorageUtil.clearLocalStorage();
      window.location.href = "/login";
    } else if (error && error.response && error.response.status === 403) {
      toast.error(error.response.data.message);
      window.location.href = "/home";
    } else {
      throw error;
    }
  }
};
