import React from 'react';

import Login from './components/Login/Login';
import Signup from './components/Signup/Signup';
import VerifyEmail from './common/utils/verifyEmail/verifyEmail'
import SignupConfirm from './common/components/signupconfirm/SignupConfirm'
import Home from "./components/Home/Home";
import AdminSetting from "./components/AdminSetting/AdminSetting";
import Admin from "./components/Admin/Admin";
import Users from "./components/Users/Users";
import Reports from "./components/Reports/Reports";
import DownloadReports from "./components/DownloadReports"
import AuthRoute from './common/helper/AuthRoute';
import GenerateReports from './components/Genratereport/Genratereport';
import ReportsProjects from './components/ReportsProjects/ReportsProjects';

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";

import './App.css';
import ProjectsView from './components/ProjectsView/ProjectsView';
import BimonthlyReport from './components/BimonthlyReport/BimonthlyReport';

function App() {
  return (
    <>
      <Router>
        <Switch>
          <AuthRoute
              exact
              path={'/home'}
              component={Home}
            />
          <AuthRoute  path={'/users'} exact component={Users} />
          <AuthRoute exact path={'/admin'}  component={Admin} />
          <AuthRoute  path={'/projectsView'} exact component={ProjectsView} />
          <AuthRoute  path={'/bimonthlyReport'} exact component={BimonthlyReport} />
          <AuthRoute  path={'/reports'} exact component={Reports} />
          <AuthRoute  path={'/downloadReports'} exact component={DownloadReports} />
          <AuthRoute  path={'/unlockDates'} exact component={AdminSetting} />
          <AuthRoute  path={'/generateReports'} exact component={GenerateReports} />
          <AuthRoute  path={'/projectReports'} exact component={ReportsProjects} />
          <Route  path={'/login'} exact component={Login} />
          <Route exact path={'/signup'} component={Signup} />
          <Route exact path={'/success'} component={SignupConfirm} />
          <Route exact path={'/email-verification/:token'} component={VerifyEmail} />
          <Redirect path="*" to="/login" />
        </Switch>
      </Router>
    </>
  );
}

export default App;
