import React, { useState } from 'react';
import { useDispatch } from "react-redux";
import styles from './Users.module.scss';
import PageContainer from '../../common/components/pagecontainer/PageContainer';
import { getAllUser } from '../../common/core/store/users/usersAction';
import { ToastContainer } from 'react-toastify';
import UsersForm from './UsersForm';
import ResetPasswordForm from './ResetPasswordForm';
import UsersTable from './UsersTable';


const Users = () => {   
    const dispatch = useDispatch();
    
    useState(() => {
        dispatch(getAllUser());
    });
    
    const [open, setOpen] = useState<boolean>(false);
    const [openPwdDlg, setOpenPwdDlg] = useState<boolean>(false);
    const [currentId, setCurrentId] = useState<number>(0);

    const handleOpen = (form: string) => {
        if(form === 'UsersForm')
            setOpen(true);
        else
            setOpenPwdDlg(true);
    };

    const handleClose = (form: string) => {
        if(form === 'UsersForm')
            setOpen(false);
        else
            setOpenPwdDlg(false);
    };

    console.log("currentId",currentId);
    return (
        <>
            <PageContainer>
                <div className={styles.mainwrapper}>
                    <div className={styles.toptitlebox}>
                        <div className={styles.leftbox}>
                            <p> User Management </p>
                        </div>
                    </div>
                    <div className={styles.mainbox}>
                        <div className={styles.usersWrapper}>
                            <UsersForm
                                open={open}
                                handleClose={handleClose}
                                currentId={currentId}
                                setCurrentId={setCurrentId}
                            />
                            <ResetPasswordForm
                                openPwdDlg={openPwdDlg}
                                handleClose={handleClose}
                                currentId={currentId}
                                setCurrentId={setCurrentId}
                            />
                            <UsersTable
                                handleOpen={handleOpen}
                                setCurrentId={setCurrentId}
                            />
                        </div>
                    </div>
                </div>
            </PageContainer>
            <ToastContainer />
        </>
    );
}

export default Users;
