import React, { useEffect } from 'react';
import { Button, Card } from '@material-ui/core';
import IconButton from '@mui/material/IconButton';
import { makeStyles } from '@material-ui/core/styles';
import AddIcon from '@material-ui/icons/Add';
import EditIcon from '@material-ui/icons/Edit';
import KeyIcon from '@mui/icons-material/Key';
import DeleteIcon from '@material-ui/icons/Delete';
import MaterialTable from '@material-table/core';
import { useDispatch, useSelector } from 'react-redux';
import { selectAllUser } from '../../common/core/store/users/usersSelector';
import { getAllUser, deleteUser, deleteUsers } from '../../common/core/store/users/usersAction';
import StorageUtil from '../../utils/StorageUtil';
import { toast } from 'react-toastify';
import * as constant from "../../common/constants/Constants";

const useStyles = makeStyles((theme) => ({
  button: {
    margin: theme.spacing(1),
  },
  card: {
    margin: '10px',
  },
  title: {
    flexGrow: 1,
    textAlign: 'center',
    fontSize: '1.96rem',
  },
}));

const UsersTable = ({ handleOpen, setCurrentId }: any) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const users = useSelector(selectAllUser);

  useEffect(() => {
    if (!users) {
      dispatch(getAllUser());
    }
  }, [users, dispatch]);

  const delUser = (id: number) => {
    dispatch(deleteUser(id));
  };

  const delUsers = (idArr: any) => {
    dispatch(deleteUsers({ ids: idArr }));
  };

  const checkPermission = () => {
    if (StorageUtil.getLocalStorage('role') !== constant.admin_role) {
      toast.error("Permission denied.");
      return false;
    }
    return true;
  }


  return (
    <>
      <div style={{ textAlign: 'right' }}>
        <Button
          variant='contained'
          color='primary'
          size='large'
          className={classes.button}
          startIcon={<AddIcon />}
          onClick={() => {
            if(!checkPermission()) return;
            handleOpen('UsersForm');
          }}
        >
          Add User
        </Button>
      </div>
      <Card>
        <MaterialTable
          title='User Details'
          columns={[
            { title: 'Name', field: 'name' },
            { title: 'Email', field: 'email' },
            { title: 'Role', field: 'roleString' },
            { title: 'Created Date', field: 'createdAt' },
            { title: 'Updated Date', field: 'updatedAt' },
            {
              title: 'Reset Password',
              field: 'edit',
              render: (rowData: any) =>
                rowData && (
                  <>
                    <IconButton
                      color='secondary'
                      onClick={() => {
                        if(!checkPermission()) return;
                        setCurrentId(rowData.id);
                        handleOpen('ResetPasswordForm');
                      }}
                    >
                      <KeyIcon />
                    </IconButton>
                  </>
                ),
            },
            {
              title: 'Edit/Delete',
              field: 'edit',
              render: (rowData: any) =>
                rowData && (
                  <>
                    <IconButton
                      color='primary'
                      onClick={() => {
                        if(!checkPermission()) return;
                        setCurrentId(rowData.id);
                        handleOpen('UsersForm');
                      }}
                    >
                      <EditIcon />
                    </IconButton>
                    <IconButton
                      color='error'
                      onClick={() => {
                        if(!checkPermission()) return;
                        delUser(rowData.id);
                      }}
                    >
                      <DeleteIcon />
                    </IconButton>
                  </>
                ),
            },
          ]}
          data={users}
          actions={[
            {
              tooltip: 'Remove All Selected Users',
              icon: 'delete',
              onClick: (evt: any, data: any) => delUsers(data.map((row: any) => row.id)),
            },
          ]}
          options={{
            actionsColumnIndex: -1,
            selection: true,
          }}
        />
      </Card>
    </>
  );
};

export default UsersTable;
