import React, { useState, useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
  DialogActions,
  TextField,
  DialogContentText,
  DialogContent,
  DialogTitle,
  Dialog,
  Button,
} from '@material-ui/core';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import { createUser, updateUser } from '../../common/core/store/users/usersAction';
import { selectUserById } from '../../common/core/store/users/usersSelector';
import styles from './Users.module.scss';
import ValidatorUtil from '../../utils/ValidatorUtil';
import { toast } from 'react-toastify';

const UsersForm = ({ currentId, setCurrentId, open, handleClose }:any) => {
  const dispatch = useDispatch();

  const initialState = {
    name: '',
    email: '',
    role: '1',
  };

  const [userData, setUserData] = useState(initialState);

  const userDetails = useSelector(selectUserById(currentId));

  const inputNameRef = useRef<any>(null);

  const inputEmailRef = useRef<any>(null);

  useEffect(() => {
    if (userDetails) setUserData(userDetails);
  }, [userDetails]);

  const clearData = () => {
    setUserData(initialState);
    setCurrentId(0);
  };
  const handleSubmit = async (e: any) =>  {
    e.preventDefault();

    let errmsg = ValidatorUtil.fieldValidator(userData.name, 'text', true);
    if(errmsg != null) {
      toast.error(errmsg, { autoClose: 3000 });
      inputNameRef.current.focus();
      return false;
    }
    errmsg = ValidatorUtil.fieldValidator(userData.email, 'email', true);
    if(errmsg != null) {
      toast.error(errmsg, { autoClose: 3000 });
      inputEmailRef.current.focus();
      return false;
    }
    
    if (currentId === 0) { 
      if(!await dispatch(createUser(userData)))
        return;
    }else{
      if(!await dispatch(updateUser(currentId, userData)))
        return;
    } 
    dlgClose();
  };
  const dlgClose = () =>{
    clearData();
    handleClose('UsersForm');
  };
  return (
    <Dialog
      open={open}
      onClose={dlgClose}
      aria-labelledby='form-dialog-title'
    >
      <DialogTitle id='form-dialog-title'>User Detail</DialogTitle>
      <DialogContent>
        <DialogContentText>
          {`To ${
            currentId === 0 ? 'add' : 'update'
          } your user detail from here`}
        </DialogContentText>

        <TextField
          autoFocus
          margin='dense'
          id='name'
          inputRef={inputNameRef}
          label='Name'
          type='text'
          fullWidth
          value={userData.name}
          onChange={(e) =>
            setUserData({ ...userData, name: e.target.value })
          }
        />       
        <TextField          
          margin='dense'
          id='email'
          inputRef={inputEmailRef}
          label='Email Address'
          type='email'
          fullWidth
          value={userData.email}
          onChange={(e) =>
            setUserData({ ...userData, email: e.target.value })
          }
        />  
        <FormControl variant="standard" fullWidth>
          <InputLabel>Role</InputLabel>
          <Select
            margin='dense'
            id='role'
            value={userData.role}
            onChange={(e) =>
              setUserData({ ...userData, role: typeof e.target.value === 'string' 
              ? e.target.value : ''})
            }
          >
            <MenuItem value={'1'}>Technician</MenuItem>
            <MenuItem value={'2'}>Project Manager</MenuItem>
            <MenuItem value={'3'}>Administrator</MenuItem>
          </Select>
       </FormControl>        
      </DialogContent>
      <DialogActions className={styles.dlgButton}>
        <Button color='primary' onClick={handleSubmit}>
          OK
        </Button>
        <Button color='secondary' onClick={dlgClose}>
          Close
        </Button>        
      </DialogActions>
    </Dialog>
  );
};

export default UsersForm;
