import React, { useState } from 'react';
import { useDispatch } from "react-redux";
import { toast } from 'react-toastify';
import * as constant from "../../constants/Constants";

import { login, signup } from '../../core/store/users/usersAction';

const LoginHoc = (OriginalComponent, data) => {
    const NewComponent = (props) => {
        const dispatch = useDispatch();

        const [passwordType, setPasswordType] = useState(data.passwordType)
        const [fields, setFields] = useState(data.fields)
        const [errors, setErrors] = useState(data.fields)

        const changeHandler = e => {
            setFields({ ...fields, [e.target.name]: e.target.value });
            setErrors({ ...errors, [e.target.name]: null });
        }

        const blurHandler = e => {
            const value = e.target.value;
            const key = e.target.name;
            const type = e.target.type;
            const isrequired = e.target.required;
            const isConfirm = e.target.getAttribute('isConfirm');

            fieldValidator(value, key, type, isrequired, isConfirm);
        }

        const isFormValid = () => {
            let check = 0;
            for (let key in errors) {
                if (errors[key] !== null || !fields[key] && errors[key] !== "") {
                    check++;
                }
            }

            if (check === 0) {
                return true;
            } else {
                return false;
            }
        }

        const fieldValidator = (value, key, type, isrequired, isConfirm) => {
            if (!value && isrequired) {
                errors[key] = constant.requiredMsg;
            } else {
                if (value && type === 'email') {
                    const reg = /^([A-Za-z0-9_\-.])+@([A-Za-z0-9_\-.])+\.([A-Za-z]{2,4})$/;
                    const inprocessReg = /^[A-Za-z0-9._%+-]+@inprocessgroup.com$/;
                    if (reg.test(value) === false) {
                        errors[key] = constant.invalidEmail;
                    }
                    else if (inprocessReg.test(value) === false) {
                        errors[key] = constant.invalidGroupEmail;
                    }
                    else {
                        errors[key] = null;
                    }
                }
                else if (!isConfirm && value && type === 'password') {
                    const reg = /^(?!^[0-9]*$)(?!^[a-zA-Z]*$)^([a-zA-Z0-9@\$=!:.#%]{8,16})$/
                    if (reg.test(value) === false) {
                        errors[key] = constant.invalidPassword;
                    } else {
                        errors[key] = null;
                    }
                }
                else if (value.length > 50) {
                    errors[key] = constant.maxLength50;
                }
                else if (isConfirm) {
                    if (value !== fields.password) {
                        errors[key] = constant.confirmMismatched;
                    } else {
                        errors[key] = null;
                    }
                }
                else {
                    errors[key] = null;
                }
            }
            setErrors({ ...errors });
        }

        const togglePassword = (type, el) => {
            passwordType[type] = el === 'text' ? 'password' : 'text'
            setPasswordType({ ...passwordType });
        }

        const submitHandler = (e) => {
            let isConfirm = false;
            // Replace with foreach
            for (var obj in fields) {
                if (obj === 'confirmPassword') {
                    isConfirm = true;
                }
                fieldValidator(fields[obj], obj, obj, true, isConfirm);
            }
            if (isFormValid() === true) {
                if (data.type && data.type === 'login') {
                    dispatch(login(fields)).then((res) => {
                        if ((res.status && res.status === 200) || (res.response && res.response.status === 200)) {
                            props.history.push('/home');
                        }
                    })
                } else {
                    dispatch(signup(fields)).then((res) => {
                        if ((res.status && res.status === 201) || (res.response && res.response.status === 201)) {
                            toast.success(constant.registrationSuccess, { autoClose: 2000 })
                            props.history.push('/success');
                        }
                    })
                }
            }
        }

        return <OriginalComponent passwordType={passwordType} fields={fields} errors={errors} changeHandler={changeHandler} submitHandler={submitHandler} blurHandler={blurHandler} togglePassword={togglePassword} isFormValid={isFormValid} fieldValidator={fieldValidator} {...props} />
    }
    return NewComponent;
}

export default LoginHoc;