import React, { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from "react-redux";
import styles from './ReportsProjects.module.scss';
import PageContainer from '../../common/components/pagecontainer/PageContainer';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { getProjects, reportGeneration } from '../../common/core/store/common/commonAction';
import { getAllProjects, getProjectReport } from '../../common/core/store/common/commonSelector';
import DatePickerCard from '../../common/lib/DatePicker/DatePicker';
import { ToastContainer } from 'react-toastify';
import moment from 'moment';
import useAutocomplete from '@material-ui/lab/useAutocomplete';
import { CSVLink } from "react-csv";


const ReportsProjects = () => {
    const dispatch = useDispatch();
    const projects = useSelector(getAllProjects);
    const data = useSelector(getProjectReport);
    const [currentData, setCurrentData] = useState<any>({
        projectId: "",
        risk : "",
        noOfAcutal : "",
        declaredProduction : "",
        effortBudgetFromProppsal : "",
        comments : "",
        dateOfStudy : new Date(),
    });

    

    const csvRef: any = useRef();

    useEffect(() => {
        if (!projects) {
            dispatch(getProjects());
        }
    }, [projects, dispatch]);

    const handleChange = (e: any, name: string) => {
            console.log(e, name);
        if (currentData) {
            let val = e.target.value;
            let current = currentData;
            if(current)
            {
                current[name] = val;
                setCurrentData(current);
            }
        }
    }

    const generateReport = () => {
        dispatch(reportGeneration(currentData));
    }

    useEffect(() => {
        if (data && csvRef && csvRef.current) {
            setTimeout(() => {
                csvRef.current.link.click()
            }, 200);
        }
    }, [data]);

    useAutocomplete({
        id: 'use-autocomplete-demo',
        options: projects,
        getOptionLabel: (option: any) => option.name,
      });

    return (
        <>
            <PageContainer>
                <div className={styles.timewrapper}>
                    <div className={styles.toptitlebox}>
                        <div className={styles.leftbox}>
                            <p> Your Profile </p>
                            <p> <strong>Setup Project </strong>  </p>
                        </div>
                        <div className={styles.rightbox}>
                        </div>
                    </div>
                    <div className={styles.mainbox}>
                        <div className={styles.mainboxinner}>
                            <div className={styles.timeentrybox}>
                                <div className={styles.entryleft}>
                                </div>
                                <div className={styles.entryright}>
                                </div>
                            </div>
                            <div className={styles.Adminselect}>
                                <p className={styles.label_custom}>Project Name</p>
                                {
                                    projects && <Autocomplete
                                    id="size-small-outlined"
                                    size="small"
                                    style={{width: 357}}
                                    options={projects}
                                    onChange={(e: any , data: any) => {console.log(data); handleChange({target:{value: data.id}}, 'projectId')}}
                                    getOptionLabel={(option: any) => option.name}
                                    renderInput={(params) => (
                                      <TextField {...params} variant="outlined" placeholder="Favorites" />
                                    )}
                                  />
                                }
                            </div>
                            <div className={styles.databox}>
                            {/* {projects && <div className={styles.inputbox} {...getRootProps()}>
                                    <p className={styles.label_custom}>Project Name *</p>
                                    <div className={styles.input_wrapper}>
                                        <input onChange={(e) => handleChange(e, "projectName")} className={styles.input_custom} {...getInputProps()} />
                                        {groupedOptions.length > 0 ? (
                                            <ul className={styles.listbox} {...getListboxProps()}>
                                            {groupedOptions.map((option: any, index: any) => (
                                                <li {...getOptionProps({ option, index })}>{option.name}</li>
                                            ))}
                                            </ul>
                                        ) : null}
                                    </div>
                                </div> } */}


                                
                            <div className={styles.inputbox}>
                                    <p className={styles.label_custom}>Risk *</p>
                                    <input onChange={(e) => handleChange(e, "risk")} className={styles.input_custom} type="text" required />
                                </div>
                                <div className={styles.inputbox}>
                                    <p className={styles.label_custom}>No of engineers *</p>
                                    <input onChange={(e) => handleChange(e, "noOfAcutal")} className={styles.input_custom} type="text" required />
                                </div>

                                <div className={styles.inputbox}>
                                    <p className={styles.label_custom}>DeclaredProduction *</p>
                                    <input onChange={(e) => handleChange(e, "declaredProduction")} className={styles.input_custom} type="text" required />
                                </div>
                                <div className={styles.inputbox}>
                                    <p className={styles.label_custom}>Effort(Budget fromProposal) *</p>
                                    <input onChange={(e) => handleChange(e, "effortBudgetFromProppsal")} className={styles.input_custom} type="text" required />
                                </div>
                                <div className={styles.inputbox}>
                                    <p className={styles.label_custom}>Comments *</p>
                                    <input onChange={(e) => handleChange(e, "comments")} className={styles.input_custom} type="text" required />
                                </div>
                                <div className={styles.inputbox}>
                                    <p className={styles.label_custom}>Date Of Study *</p>
                                    <DatePickerCard onDateChange={(e: any) => {handleChange({ target: { value: moment.utc(e) } }, "dateOfStudy")}} isSingle={true} defaultDate={new Date()} />
                                </div>
                            </div>
                        </div>
                        <div className={styles.messagebox}>
                            <div className={styles.totalweek}>

                            </div>
                            <div className={styles.infobox}>
                                <div className={styles.updatebox}>
                                    <button onClick={generateReport} className={styles.themeButton}> Generate Report </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </PageContainer>
            <ToastContainer />
            {
                data &&
                <CSVLink filename={`${new Date().getTime()}.csv`} ref={csvRef} className={styles.displayNone} data={data && data.name} headers={data && data.field}>
                    Download me
                </CSVLink>
            }
        </>
    );
}

export default ReportsProjects;
