import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import {
  DialogActions,
  TextField,
  DialogContent,
  DialogTitle,
  Dialog,
  Button,
} from '@material-ui/core';
import { resetPassword } from '../../common/core/store/users/usersAction';
import styles from './Users.module.scss';
import ValidatorUtil from '../../utils/ValidatorUtil';
import { toast } from 'react-toastify';

const ResetPasswordForm = ({ currentId, setCurrentId, openPwdDlg, handleClose }:any) => {
  const dispatch = useDispatch();  

  const [password, setPassword] = useState<string>('');

  const clearData = () => {
    setPassword('');
    setCurrentId(0);
  };
  const handleSubmit = async (e: any) =>  {
    e.preventDefault();   

    let errmsg = ValidatorUtil.fieldValidator(password, 'password', true);
    if(errmsg != null) {
      toast.error(errmsg, { autoClose: 3000 });
      return false;
    }
    
    if(!await dispatch(resetPassword(currentId, {password})))
      return;
    
    handleClose('ResetPasswordForm');
    clearData();
  };
  const dlgClose = () =>{
    clearData();
    handleClose('ResetPasswordForm');
  };
  return (
    <Dialog
      open={openPwdDlg}
      onClose={dlgClose}
      aria-labelledby='form-dialog-title'
    >
      <DialogTitle id='form-dialog-title'>Input password</DialogTitle>
      <DialogContent>
        <TextField
          autoFocus
          margin='dense'
          id='password'
          label='Password'
          type='password'
          fullWidth
          value={password}
          onChange={(e) =>
            setPassword(e.target.value)
          }
        />     
      </DialogContent>
      <DialogActions className={styles.dlgButton}>
        <Button color='primary' onClick={handleSubmit}>
          OK
        </Button>
        <Button color='secondary' onClick={dlgClose}>
          Close
        </Button>        
      </DialogActions>
    </Dialog>
  );
};

export default ResetPasswordForm;
