import React, { useEffect } from 'react';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import LockOpenIcon from '@material-ui/icons/LockOpen';
import PermIdentityIcon from '@material-ui/icons/PermIdentity';
import RemoveRedEyeIcon from '@material-ui/icons/RemoveRedEye';
import VisibilityOffIcon from '@material-ui/icons/VisibilityOff';
import { Link } from "react-router-dom";

import { ToastContainer } from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';

import LoginHoc from '../../common/utils/LoginHoc/LoginHoc';
import StorageUtil from '../../utils/StorageUtil';
import Logo from '../../assets/images/logo.png';
import styles from './Signup.module.scss';

const passwordType = { password: 'password', confirmPassword: 'password' };
const fields = { name: null, email: null, password: null, confirmPassword: null }

const Signup = (props: any) => {
    useEffect(() => {
        if (StorageUtil.getLocalStorage('isLoggedIn'))
            props.history.push('/home');
    }, [props.history])
    
    return (
        <>
            <ToastContainer />
            <div className={styles.loginbg}>   </div>
            <div className={styles.loginwrapper}>
                <div className={styles.logo}>
                    <img src={Logo} alt="Logo" title="Logo" />
                </div>
                <div className={styles.loginbox}>
                    <p> Already have an account? <Link to="/login">Log in</Link> </p>
                    <div className={styles.inputfieldbox}>
                        <input
                            type="text"
                            id="name"
                            name="name"
                            placeholder="Name"
                            className={styles.loginfield}
                            onChange={props.changeHandler}
                            onBlur={props.blurHandler}
                            required={true}
                        />
                        <PermIdentityIcon />
                        <span className={styles.error}> {props.errors.name}  </span>
                    </div>
                    <div className={styles.inputfieldbox}>
                        <input
                            type="email"
                            id="email"
                            name="email"
                            placeholder="Email Address"
                            className={styles.loginfield}
                            onChange={props.changeHandler}
                            onBlur={props.blurHandler}
                            required={true}
                        />
                        <MailOutlineIcon />
                        <span className={styles.error}>  {props.errors.email} </span>
                    </div>
                    <div className={styles.inputfieldbox}>
                        <input
                            type={props.passwordType.password}
                            id="password"
                            name="password"
                            placeholder="Password"
                            className={styles.loginfield}
                            onChange={props.changeHandler}
                            onBlur={props.blurHandler}
                            required={true}
                        />
                        <LockOpenIcon />
                        <div className={styles.eyeicon} onClick={() => { props.togglePassword('password', props.passwordType.password) }}> {props.passwordType.password === 'text' ? <VisibilityOffIcon /> : <RemoveRedEyeIcon />}  </div>
                        <span className={styles.error}> {props.errors.password} </span>
                    </div>
                    <div className={styles.inputfieldbox}>
                        <input
                            type={props.passwordType.confirmPassword}
                            id="confirmPassword"
                            name="confirmPassword"
                            placeholder="Confirm password"
                            className={styles.loginfield}
                            onChange={props.changeHandler}
                            onBlur={props.blurHandler}
                            required={true}
                        />
                        <LockOpenIcon />
                        <div className={styles.eyeicon} onClick={() => { props.togglePassword('confirmPassword', props.passwordType.confirmPassword) }}> {props.passwordType.confirmPassword === 'text' ? <VisibilityOffIcon /> : <RemoveRedEyeIcon />} </div>
                        <span className={styles.error}> {props.errors.confirmPassword} </span>
                    </div>
                    <div className={styles.buttonbox}>
                        <button onClick={props.submitHandler} className={styles.signButton}>
                            Sign Up
                        </button>
                    </div>
                </div>
            </div>
        </>
    );
}

export default LoginHoc(Signup, { fields: fields, passwordType: passwordType });    