import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from "react-redux";
import styles from './Admin.module.scss';
import PageContainer from '../../common/components/pagecontainer/PageContainer';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { getProjects, updateProject } from '../../common/core/store/common/commonAction';
import { getAllProjects } from '../../common/core/store/common/commonSelector';
import { toast, ToastContainer } from 'react-toastify';
import Grid from '@mui/material/Grid';
import MilestonesTable from './Milestones/MilestonesTable';
import ValidatorUtil from '../../utils/ValidatorUtil';
import { getMilestones } from '../../common/core/store/milestones/milestonesAction';
import { selectMilestones } from '../../common/core/store/milestones/milestonesSelector';

const Admin = () => {
    const dispatch = useDispatch();
    const projects = useSelector(getAllProjects);
    const [currentData, setCurrentData] = useState<any>([]);
    const [milestonesData, setMilestonesData] = useState<any>([]);
    const [projectId, setProjectId] = useState<number>(0);
    const milestones = useSelector(selectMilestones);

    console.log("projects", projects);

    useEffect(() => {
        if (!projects) {
            dispatch(getProjects());
        } else {
            setCurrentData(projects[0]);
            setProjectId(projects[0].id);
        }
    }, [projects, dispatch]);

    useEffect(() => {
        if (projectId !== 0) {
            console.log("projectId", projectId);
            (async () =>
                await dispatch(getMilestones(projectId)))();
        }
    }, [projectId, dispatch]);

    useEffect(() => {
        setMilestonesData(milestones);
        console.log("milestones", milestones)
    }, [milestones, dispatch]);

    const handleChange = (e: any, name: string, isNumber: boolean) => {
        if (currentData) {
            let val = e.target.value;
            if (isNumber)
                val = parseInt(val.replaceAll(",", ""));
            let current = currentData;
            console.log("handleChange", name, val);
            if (current) {
                current[name] = val;
                setCurrentData(current);
            }
        }
    }

    const validate = () => {
        let errmsg = ValidatorUtil.fieldValidator(currentData.engineers_need, 'number', true);
        if (errmsg != null) {
            toast.error("Please Input Engineers needed field correctly.", { autoClose: 3000 });
            return false;
        }
        errmsg = ValidatorUtil.fieldValidator(currentData.opportunities, 'text', true);
        if (errmsg != null) {
            toast.error("Please Input Immediate opportunities field.", { autoClose: 3000 });
            return false;
        }
        errmsg = ValidatorUtil.fieldValidator(currentData.pm_involvement, 'percent', true);
        if (errmsg != null) {
            toast.error("Please Input PM / LE involvement field correctly.", { autoClose: 3000 });
            return false;
        }
        if (milestonesData.length === 0) {
            toast.error("Please Add Milestones.", { autoClose: 3000 });
            return false;
        }
        let isValid = true;
        milestonesData.map((milestone: any) => {
            if (!isValid) {
                return false;
            }
            errmsg = ValidatorUtil.fieldValidator(milestone.name, 'text', true);
            if (errmsg != null) {
                toast.error("Please Milestone Name field correctly.", { autoClose: 3000 });
                isValid = false;
                return false;
            }
            errmsg = ValidatorUtil.fieldValidator(milestone.progress, 'percent', true);
            if (errmsg != null) {
                toast.error("Please Milestone Progress field correctly.", { autoClose: 3000 });
                isValid = false;
                return false;
            }
            return true;
        });
        return isValid;
    }

    const updateHandler = () => {
        console.log(currentData);
        if (currentData) {
            if (!validate())
                return;
            const data = {
                declaredIncome: currentData.declaredIncome,
                hoursProposed: currentData.hoursProposed,
                comment: currentData.comment,
                noOfAcutal: currentData.noOfAcutal,
                type: currentData.type,
                dcs: currentData.dcs,
                license: currentData.license,
                license_necessary: currentData.license_necessary,
                risk_level: currentData.risk_level,
                engineers_need: currentData.engineers_need,
                opportunities: currentData.opportunities,
                pm_involvement: currentData.pm_involvement,
                milestones: milestonesData,
            }
            console.log("data", data);
            dispatch(updateProject(currentData.id, data));
        }
    }

    return (
        <>
            <PageContainer>
                <div className={styles.timewrapper}>
                    <div className={styles.toptitlebox}>
                        <div className={styles.leftbox}>
                            <p> Your Profile </p>
                            <p> <strong>Setup Project </strong>  </p>
                        </div>
                        <div className={styles.rightbox}>
                        </div>
                    </div>
                    <div className={styles.mainbox}>
                        <div className={styles.mainboxinner}>
                            <div className={styles.timeentrybox}>
                                <div className={styles.entryleft}>
                                </div>
                                <div className={styles.entryright}>
                                </div>
                            </div>
                            <div className={styles.Adminselect}>
                                <p className={styles.label_custom}>Project Name</p>
                                {projects && <Autocomplete
                                    id="projects"
                                    options={projects}
                                    className={styles.selectReport}
                                    getOptionLabel={(option) => option.name}
                                    style={{ width: 500 }}
                                    defaultValue={projects[0]}
                                    title={currentData && currentData.name}
                                    onChange={(e, data) => { console.log("selectChange", data); setCurrentData(data); setProjectId(data.id); }}
                                    renderInput={(params) => <TextField {...params} variant="outlined" />}
                                />}
                            </div>
                            <div className={styles.dataBox}>
                                <span>Data - Manual Entry</span>
                                <Grid container spacing={2}>
                                    <Grid item xs={7}>
                                        <div className={styles.entryDataBox}>
                                            <div className={styles.entryLeftBox}>
                                                <div className={styles.inputbox}>
                                                    <p className={styles.label_custom}>Project Type *</p>
                                                    <input defaultValue={currentData && currentData.type} onChange={(e) => handleChange(e, "type", false)} className={styles.input_custom} type="text" required />
                                                </div>
                                                <div className={styles.inputbox}>
                                                    <p className={styles.label_custom}>Hours Proposed *</p>
                                                    <input defaultValue={currentData && currentData.hoursProposed} onChange={(e) => handleChange(e, "hoursProposed", true)} className={styles.input_custom} type="text" required />
                                                </div>
                                                <div className={styles.inputbox}>
                                                    <p className={styles.label_custom}>Project Value *</p>
                                                    <input defaultValue={currentData && currentData.declaredIncome} onChange={(e) => handleChange(e, "declaredIncome", true)} className={styles.input_custom} type="text" required />
                                                </div>
                                            </div>
                                            <div className={styles.entryLeftBox}>
                                                <div className={styles.inputbox}>
                                                    <p className={styles.label_custom}>No of Planned Engineers *</p>
                                                    <input defaultValue={currentData && currentData.noOfAcutal && currentData.noOfAcutal} onChange={(e) => handleChange(e, "noOfAcutal", true)} className={styles.input_custom} type="text" required />
                                                </div>
                                                <div className={styles.inputbox}>
                                                    <p className={styles.label_custom}>DCS *</p>
                                                    <input defaultValue={currentData && currentData.dcs && currentData.dcs} onChange={(e) => handleChange(e, "dcs", false)} className={styles.input_custom} type="text" required />
                                                </div>
                                            </div>
                                            <div className={styles.entryLeftBox}>
                                                <div className={styles.inputbox}>
                                                    <p className={styles.label_custom}>N License Actual *</p>
                                                    <input defaultValue={currentData && currentData.license && currentData.license} onChange={(e) => handleChange(e, "license", true)} className={styles.input_custom} type="text" required />
                                                </div>
                                                <div className={styles.inputbox}>
                                                    <p className={styles.label_custom}>N License Necessary *</p>
                                                    <input defaultValue={currentData && currentData.license_necessary && currentData.license_necessary} onChange={(e) => handleChange(e, "license_necessary", false)} className={styles.input_custom} type="text" required />
                                                </div>
                                            </div>
                                            <div className={styles.CommentBox}>
                                                <div className={styles.inputbox}>
                                                    <p className={styles.label_custom}>Comments *</p>
                                                    <TextField
                                                        id="outlined-multiline-static"
                                                        multiline
                                                        rows={4}
                                                        defaultValue={currentData && currentData.comment}
                                                        inputProps={{ maxLength: 200 }}
                                                        style={{ border: "none", width: "100%" }}
                                                        onChange={(e) => handleChange(e, "comment", false)}
                                                        required
                                                    />
                                                </div>
                                            </div>
                                        </div>
                                    </Grid>
                                    <Grid item xs={5}>
                                        <div className={styles.entryDataBox}>

                                            <div className={styles.entryLeftBox}>
                                                <div className={styles.inputbox}>
                                                    <p className={styles.label_custom}>Risk level*</p>
                                                    <select className={styles.select_custom} onChange={(e) => handleChange(e, "risk_level", true)}>
                                                        <option value='1'>1</option>
                                                        <option value='2'>2</option>
                                                        <option value='3'>3</option>
                                                        <option value='4'>4</option>
                                                        <option value='5'>5</option>
                                                    </select>
                                                </div>
                                                <div className={styles.inputbox}>
                                                    <p className={styles.label_custom}>Engineers needed *</p>
                                                    <input defaultValue={currentData && currentData.engineers_need && currentData.engineers_need} onChange={(e) => handleChange(e, "engineers_need", true)} className={styles.input_custom} type="text" required />
                                                </div>
                                            </div>
                                            <div className={styles.entryLeftBox}>
                                                <div className={styles.inputbox}>
                                                    <p className={styles.label_custom}>Immediate opportunities*</p>
                                                    <input defaultValue={currentData && currentData.opportunities && currentData.opportunities} onChange={(e) => handleChange(e, "opportunities", false)} className={styles.input_custom} type="text" required />
                                                </div>
                                                <div className={styles.inputbox}>
                                                    <p className={styles.label_custom}>PM / LE involvement(%) *</p>
                                                    <input defaultValue={currentData && currentData.pm_involvement && currentData.pm_involvement} onChange={(e) => handleChange(e, "pm_involvement", true)} className={styles.input_custom} type="text" required />
                                                </div>
                                            </div>
                                            <div className={styles.padding} />
                                            <div className={styles.entryLeftBox}>
                                                <div className={styles.inputbox}>
                                                    <p className={styles.label_custom}>Milestones*</p>
                                                    <MilestonesTable rowsData={milestonesData} setRowsData={setMilestonesData} projectId={currentData.id} />
                                                </div>
                                            </div>
                                        </div>
                                    </Grid>
                                </Grid>



                            </div>
                        </div>
                        {currentData &&
                            <div className={styles.messagebox}>

                                <div className={styles.infobox}>
                                    <div className={styles.updatebox}>
                                        <button onClick={updateHandler} className={styles.themeButton}> Update </button>
                                    </div>
                                </div>
                            </div>
                        }
                    </div>
                </div>
            </PageContainer>
            <ToastContainer />

        </>
    );
}

export default Admin;
