import React, { useEffect } from 'react';
import { useDispatch, useSelector } from "react-redux";
import styles from './AdminSetting.module.scss';
import PageContainer from '../../common/components/pagecontainer/PageContainer';
import { getSettingData, saveSettingData } from '../../common/core/store/common/commonAction';
import { getSettingDate } from '../../common/core/store/common/commonSelector';
import { toast, ToastContainer } from 'react-toastify';

const AdminSetting = () => {
    const dispatch = useDispatch();
    const setting = useSelector(getSettingDate);

    useEffect(() => {
        if (!setting) {
            dispatch(getSettingData());
        }
    }, [setting, dispatch]);

    const updateHandler_fn = () => {
        dispatch(saveSettingData());
        toast.success("Updated successfully");
        setTimeout(() => {
            dispatch(getSettingData());
        }, 200)
    }

    return (
        <>
            <PageContainer>
                <div className={styles.timewrapper}>
                    <div className={styles.toptitlebox}>
                        <div className={styles.leftbox}>
                            <p> Lock/Unlock Dates </p>
                        </div>
                        <div className={styles.rightbox}>
                        </div>
                    </div>
                    <div className={styles.mainbox}>
                        <div className={styles.mainboxinner}>
                            <div className={styles.timeentrybox}>
                                <div className={styles.entryleft}>
                                </div>
                                <div className={styles.entryright}>
                                </div>
                            </div>
                            <div className={styles.databox}>
                                <p>Lock/Unlock Dates</p>
                                {setting && setting.dateOvveride ? <p>Everyone will be able to modify any project hours. Please click confirm to unlock timesheets.</p> : <><p className={styles.red} >The timesheets are unlocked and everyone will be able to modify any projects hours.</p><p>Please click confirm to Lock the timesheets.</p></> }
                                <button onClick={updateHandler_fn} className={styles.themeButton}> Confirm </button>
                                <br/>
                            </div>
                        </div>
                    </div>
                </div>
            </PageContainer>
            <ToastContainer />
        </>
    );
}

export default AdminSetting;
