import React from "react";
import MilestonesTableRows from "./MilestonesTableRows"
import styles from '../Admin.module.scss';

interface Props {
    rowsData: any
    setRowsData: (rowsData: any) => void
    projectId: number,
}

const MilestonesTable: React.FC<Props> = ({ rowsData, setRowsData, projectId }) => {
    const addTableRows = () => {
        const milestone = {
            name: '',
            progress: 0,
            date: '',
            projectId,
        }
        setRowsData([...rowsData, milestone])
    }
    const deleteTableRows = (index: number) => {
        const rows = [...rowsData];
        rows.splice(index, 1);
        setRowsData(rows);
    }
    const handleChange = (index: number, event: any) => {
        const { name, value } = event.target;
        const milestone = [...rowsData];
        milestone[index][name] = value;
        setRowsData(milestone);
    }
    return (
        <div className={styles.milestoneContainer}>
            <table>
                <thead>
                    <tr>
                        <th>Name</th>
                        <th>Progress(%)</th>
                        <th>Date</th>
                        <th className={styles.addButton} onClick={addTableRows}>Add</th>
                    </tr>
                </thead>
                <tbody>
                    <MilestonesTableRows rowsData={rowsData} deleteTableRows={deleteTableRows} handleChange={handleChange} />
                </tbody>
            </table>
        </div>
    )
}
export default MilestonesTable