import React, { useEffect } from 'react';
import SearchIcon from '@material-ui/icons/Search';
import NotificationsNoneIcon from '@material-ui/icons/NotificationsNone';
import HelpOutlineIcon from '@material-ui/icons/HelpOutline';
import Avatar from '@material-ui/core/Avatar';
import StorageUtil from '../../../utils/StorageUtil';
import Home from '../../../assets/images/homedashboard.png';
import Line from '../../../assets/images/line.png';
import MobMenu from '../../../assets/images/menu-white.png';
import UserProfile from '../../../assets/images/user-profile.png'
import { Link } from "react-router-dom";

import styles from './Header.module.scss';
import { toast } from 'react-toastify';

const Header = () => {
    const logout = () => {
        StorageUtil.clearLocalStorage();
        toast.success("Logout successfully", { autoClose: 2000 });
        window.location.href = '/'
    }

    useEffect(() => {
        document.body.style.background = '#f1f3f9';
    }, [])

    const valChecker = () => {
        const Ar = [47, 24, 61, 62, 26, 63, 39, 49, 80, 67, 27, 51];
        let id: string = StorageUtil.getLocalStorage('id') ? StorageUtil.getLocalStorage('id') : "";
        return Ar.indexOf(parseInt(id)) > -1 ? true : false
    }

    return (
        <div className={styles.header}>
            <div className={styles.headerleft}>
                <div className={styles.mobmenu}>
                    <img src={MobMenu} alt="Menu" />
                </div>
                <h1> Home </h1>
                <ul className={styles.menu}>
                    <li className={styles.home}> <a href="/"> <img src={Home} alt="Home" title="Home" />  Home </a></li>
                    <li className={styles.dashboard}> <a href="/"> <img src={Line} alt="Line" title="Line" />  dashboard</a></li>
                </ul>
                <div className={styles.searchbox}>
                    <input type="text" name="search" placeholder="Search" className={styles.search} />
                    <SearchIcon />
                </div>
                <div className={styles.unlockBox}>
                    {
                        valChecker() && (
                            <>
                                <Link to="/unlockDates"> Unlock Dates </Link>
                            </>
                        )

                    }
                </div>
            </div>
            <div className={styles.headerright}>
                <NotificationsNoneIcon />
                <HelpOutlineIcon />
                <div className={styles.profilebox}>
                    <Avatar alt={StorageUtil.getLocalStorage('name')} src={UserProfile} />
                    {StorageUtil.getLocalStorage('name')}
                </div>
                <button onClick={logout} className={styles.themeButton}> Logout </button>
            </div>
        </div>
    );
}

export default Header;