/* eslint-disable react-hooks/exhaustive-deps */
import { useEffect, useRef, useState } from "react";

import { useDispatch, useSelector } from 'react-redux';

import { getAllProjects, getProjectReportById } from '../../common/core/store/common/commonSelector';
import { getProjects, projectReportById } from '../../common/core/store/common/commonAction';


export default function GenerateController() {
    const projects = useSelector(getAllProjects);
    const data = useSelector(getProjectReportById);
    const [pId, setPid] = useState<number>();
    const [commentBoxes, setCommentBoxValues] = useState<Array<Array<{ text: string, color: string }>>>([
        [], [], [], []
    ])

    const [handler, setHandler] = useState(false)
    const [type, setType] = useState(1);
    const dispatch = useDispatch();

    const commentBox1: any = useRef(null);
    const commentBox2: any = useRef(null);
    const commentBox3: any = useRef(null);
    const commentBox4: any = useRef(null);

    useEffect(() => {
        setHandler(false);
    }, [handler])

    useEffect(() => {
        if (!projects) {
            dispatch(getProjects());
        }
        if (projects) {
            setPid(projects[0].id);
        }
    }, [projects]);

    useEffect(() => {
        if (pId && type) {
            dispatch(projectReportById({ pId, type }));
        }
    }, [pId, type])

    const changeProject = (value: number) => {
        setPid(value);
    }

    const typeHandler = (value: number) => {
        setType(value)
    }

    const changeHandler = (index: number) => {
        // const array = commentBoxes;
        let ref;

        switch (index) {
            case 0:
                ref = commentBox1;
                break;
            case 1:
                ref = commentBox2;
                break;
            case 2:
                ref = commentBox3;
                break;
            default:
                ref = commentBox4;
                break;
        }

        const val = ref.current.value;
        commentBoxes[index].push({
            text: val,
            color: ""
        })
        setCommentBoxValues(commentBoxes);
        setHandler(true);
        ref.current.value = "";

    }

    const setColor = (color: string, index: number, boxIndex: number) => {
        commentBoxes[boxIndex][index].color = color
        setCommentBoxValues(commentBoxes);
        setHandler(true);
    }

    return {
        projects,
        data,
        type,
        commentBoxes,
        commentBox1,
        commentBox2,
        commentBox3,
        commentBox4,
        setColor,
        changeHandler,
        typeHandler,
        changeProject
    }
}