import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Highcharts from "highcharts";
import HighchartsReact from "highcharts-react-official";
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import Typography from '@material-ui/core/Typography';
import Box from "@material-ui/core/Box";
import styles from './Reports.module.scss';

import Button from '@material-ui/core/Button';
import RefreshIcon from '@material-ui/icons/Refresh';

import { getProjectReport, getProjects, refreshGraph } from '../../common/core/store/common/commonAction';
import { getProjectByReport, getAllProjects, getCelexoisApiStatus } from '../../common/core/store/common/commonSelector';

require("highcharts/modules/exporting")(Highcharts);
require("highcharts/modules/export-data")(Highcharts);

const ReportData = () => {
  const dispatch = useDispatch();
  const graphData = useSelector(getProjectByReport);
  const projects = useSelector(getAllProjects);
  const apiStatus = useSelector(getCelexoisApiStatus);
  const [currentId, setCurrentId] = useState(0);
  const [projectName, setProjectName] = useState("");

  const getProjecReport_fn = (id: number, name: string) => {
    dispatch(getProjectReport(id));
    setCurrentId(id);
    setProjectName(name);
  }

  const duration = graphData && graphData.totalMonth
  const xSeries = [
    {
      "showInLegend": true,
      "name": "Time Entry",
      "data": graphData && graphData.actual
    },
    {
      "showInLegend": true,
      "name": "Baseline",
      "data": graphData && graphData.estimated
    },
    {
      "showInLegend": true,
      "name": "Projection From Celoxis",
      "data": graphData && graphData.celoxis
    },
  ];
  const options = {
    title: {
      useHTML: true,
      style: {
        padding: "5px 10px",
        fontSize: "13px",
        color: "#666666",
        textTransform: "uppercase",
        fontWeight: "600",
      },
      text: "",
      align: "left",
    },
    credits: {
      enabled: false,
    },
    yAxis: {
      title: {
        text: " ",
      },
    },
    xAxis: {
      categories: duration,
    },

    legend: {
      layout: "horizontal",
      align: "center",
      verticalAlign: "bottom",
    },

    plotOptions: {
      line: {
        dataLabels: {
          enabled: true,
        },
        enableMouseTracking: true,
      },
    },
    tooltip: {
      shared: true,
      split: false,
    },
    series: xSeries,
    responsive: {
      rules: [
        {
          condition: {
            width: 100,
          },
          chartOptions: {
            legend: {
              layout: "horizontal",
              align: "center",
              verticalAlign: "bottom",
            },
          },
        },
      ],
    },
    exporting: {
      buttons: {
        contextButton: {
          enabled: false,
        },
      },
    },
  };

  React.useEffect(() => {
    if (!projects) {
      dispatch(getProjects());
    } else {
      setCurrentId(projects[0].id);
      getProjecReport_fn(projects[0].id, projects[0].name);
    }
  }, [projects, dispatch]);

  React.useEffect(() => {
    if (!apiStatus && currentId) {
      getProjecReport_fn(currentId, projectName);
    }
  }, [apiStatus])

  const refreshFunction = async () => {
    dispatch(refreshGraph());
  }

  return <>
    <div className={styles.Adminselect}>
      <p className={styles.label_custom}>Project Name</p>
      {projects && <Autocomplete
        id="projects"
        options={projects}
        title={projectName}
        className={styles.selectReport}
        getOptionLabel={(option) => option.name}
        style={{ width: 500, marginRight: '10px' }}
        defaultValue={projects[0]}
        onChange={(e, data) => { data && getProjecReport_fn(data.id, data.name) }}
        renderInput={(params) => <TextField {...params} label="Project" variant="outlined" />}
      />}
      <Button
        variant="contained"
        color="primary"
        disabled={apiStatus}
        startIcon={<RefreshIcon />}
        onClick={refreshFunction}
      >
        {apiStatus ? "Getting" : "Get"}  Data From Celoxis
      </Button>
      <Box p={2}>
        {apiStatus &&
          <Typography variant="h5" component="h2">
            (Note: Refresh might take Upto 3 minutes for processing.)
          </Typography>
        }
      </Box>
    </div>
    <div className={styles.infoTableWrapper}>
      <div className={styles.genInfoTable}>
        <h1> GENERAL INFORMATION </h1>
        <table>
          <thead>
            <tr>
              <th> PROJECT NUM. </th>
              <th> CLIENT </th>
              <th> DESCRIPTION </th>
              <th> P.MANAGER </th>
              <th> Project Value </th>
              <th> Type  </th>
              <th> DCS </th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td> {graphData && graphData.celxiosData.code} </td>
              <td> {graphData && graphData.celxiosData.clients && graphData.celxiosData.clients.data[0].name}  </td>
              <td>{graphData && graphData.celxiosData.description}</td>
              <td>  {graphData && graphData.celxiosData.manager && graphData.celxiosData.manager.data.name} </td>
              <td> {graphData && graphData.project.declaredIncome.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")} </td>
              <td>  {graphData && graphData.project.type} </td>
              <td> {graphData && graphData.project.dcs} </td>
            </tr>

          </tbody>
        </table>
      </div>

      <div className={styles.genInfoTable}>
        <h1> RESOURCES </h1>
        <table>
          <thead>
            <tr>
              <th colSpan={2}> ENGINEERS </th>
              <th colSpan={3}> LICENSES </th>
            </tr>
          </thead>
          <tbody>

            <tr>
              <td> Planificado </td>
              <td> Real  </td>
              <td>  Software (HYSYS, UNISIM, etc) </td>
              <td> N.Licencias (actual) </td>
              <td>  N. Licencias (necesarias) </td>
            </tr>

            <tr>
              <td> {graphData && graphData.project.noOfAcutal} </td>
              <td> {graphData && graphData.realNoEng}  </td>
              <td>  {graphData && graphData.celxiosData.custom_proc_sim} </td>
              <td> {graphData && graphData.project.license} </td>
              <td>  {graphData && graphData.project.license_necessary} </td>
            </tr>

          </tbody>
        </table>
      </div>

      <div className={styles.genInfoTable}>
        <h1> PROJECTION </h1>
        <table>
          <thead>
            <tr >
              <th colSpan={3}> % Completed </th>
              <th colSpan={1}> Planned % Projected </th>
              <th colSpan={2}> Total Hours </th>
              <th colSpan={2}> Dates </th>
            </tr>
          </thead>
          <tbody>

            <tr>
              <td> Partial % </td>
              <td> Planned   </td>
              <td> Real   </td>
              <td> (Next Month)  </td>
              <td>  Proposal </td>
              <td> Planned  </td>
              <td> Started  </td>
              <td> Projected Finish   </td>
            </tr>
            <tr>
              <td> {graphData && graphData.partial}% </td>
              <td> {graphData && graphData.celxiosData.plannedPercentComplete}%   </td>
              <td> {graphData && graphData.celxiosData.actualPercentComplete}%   </td>
              <td> {graphData && graphData.number}% </td>
              <td> {graphData && graphData.project.hoursProposed} </td>
              <td> {graphData && graphData.celxiosData.plannedEffort}  </td>
              <td> {graphData && graphData.celxiosData.plannedStart}  </td>
              <td> {graphData && graphData.celxiosData.plannedFinish}   </td>
            </tr>

          </tbody>
        </table>
      </div>

      <div className={styles.genInfoTable}>
        <h1> MILESTONES  </h1>
        <table>
          <thead>
            <tr>
              <th> Milestones </th>
              <th> Description </th>
              <th> Date </th>
              <th> % Project Value  </th>
              <th> Status </th>

            </tr>
          </thead>
          <tbody>

              {graphData && graphData.tasks.map((res: any, index: number) => <tr>
              <td> MS{index + 1} </td>
              <td> {res.name}  </td>
              <td> {res.plannedFinish} </td>
              <td>  {res.actualPercentComplete}% </td>
              <td> {parseInt(res.actualPercentComplete) === 100 ? "Billed" : "Pending"} </td>
            </tr>)}

          </tbody>
        </table>
      </div>
      <div className={styles.commentBox}>
        <h2> General Comments  </h2>
        <p> {graphData && graphData.project.comment} </p>
      </div>

    </div>

    <HighchartsReact
      highcharts={Highcharts}
      options={options}
    />
  </>
}
export default ReportData;