import React, { useRef, useState } from "react";
import dayjs from "dayjs";
import {
    TextField,
    InputLabel,
    MenuItem,
    FormControl,
    Select,
} from "@material-ui/core";
import Autocomplete from "@material-ui/lab/Autocomplete";
import ListItemText from '@material-ui/core/ListItemText';
import Checkbox from '@material-ui/core/Checkbox';


import { PDFExport } from "@progress/kendo-react-pdf";

import PageContainer from "../../common/components/pagecontainer/PageContainer";
import styles from "./Genratereport.module.scss";
import Loader from "../../../src/assets/images/loader.svg";

import GenerateController from "./generateController";
const Genratereport = () => {
    const {
        projects,
        data,
        type,
        commentBoxes,
        commentBox1,
        commentBox2,
        commentBox3,
        commentBox4,
        setColor,
        changeHandler,
        typeHandler,
        changeProject,
    } = GenerateController();

    const pdfExportComponent: any = useRef();
    const [idDownloading, setSdDownloading] = useState(false);
    const [reportNo, setReport] = useState("");
    const [value, setValue] = useState("");
    const handleExportWithComponent = (event: any) => {
        setSdDownloading(true);
        document.body.style.overflow = 'hidden';
        setTimeout(() => {
            pdfExportComponent.current.save();
            setSdDownloading(false);
            document.body.style.overflow = 'auto';
        }, 1000);
    };

    const commentBoxHtml = (index: number, ref: any, heading: string) => {
        return <div className={`${styles.progressDescBox} ${styles.customerBox} ${styles.commentwrapper}`}>
            <h3> {heading}</h3>
            <ul className={styles.selectDataList}>
                {
                    commentBoxes[index]?.map((res, ind) =>
                        <li>
                            <span className={styles.selectContent}>{ind + 1}. {res.text}</span>
                            <div className={styles.selectColorBox}>
                                <span onClick={() => { setColor("green", ind, index) }} className={res.color === "green" ? styles.BackGreen : styles.BackLgreen}></span> <span onClick={() => { setColor("red", ind, index) }} className={res.color === "red" ? styles.BackRed : styles.BackPink}></span>
                            </div>
                        </li>
                    )
                }
            </ul>
            <div className={styles.commentBox}>
                <textarea id="" ref={ref} rows={3} cols={70} />
                <button onClick={() => {
                    changeHandler(index)
                }} > Add new </button>
            </div>
        </div>
    }
    
    const [workPlan, setworkPlan] = React.useState<string[]>([]);
    const [newPlanReport, setNewPlanReport] = React.useState<string[]>([]);

    const handleChange = (event: React.ChangeEvent<{ value: unknown }>) => {
        setworkPlan(event.target.value as string[]);
    };

    const handleChange2 = (event: React.ChangeEvent<{ value: unknown }>) => {
        setNewPlanReport(event.target.value as string[]);
    };



    console.log(workPlan);
    const ITEM_HEIGHT = 48;
    const ITEM_PADDING_TOP = 8;
    const MenuProps = {
        PaperProps: {
            style: {
                maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
                width: 250,
            },
        },
    };

    const dilverables = () => {
        return <ul className={styles.first}>
            <h3> Deliverables / Document: </h3>
            <h4> # &nbsp;&nbsp;&nbsp;&nbsp;Deliverable </h4>
            {
                data &&
                data.tasks &&
                data.tasks.map((res: any, index: number) =>
                    <li>D{index + 1}&nbsp;&nbsp;  {res.name}</li>

                )
            }
        </ul>
    }

    return (
        <>
            {idDownloading && (
                <div className={styles.loaderwrapper}>
                    <div className={styles.loaderinner}>
                        <img alt="loader" src={Loader} />
                    </div>
                </div>
            )}
            <PageContainer>
                <div className={styles.timewrapper}>
                    <div className={styles.toptitlebox}>
                        <div className={styles.leftbox}>
                            <p> Projects Report </p>
                        </div>
                        <div className={styles.rightbox}></div>
                    </div>
                    <div className={styles.mainbox}>
                        <div className={styles.mainboxinner}>
                            <div className={styles.viewSelect}>
                                <p> View report </p>
                                {projects && (
                                    <>
                                        <Autocomplete
                                            id="projects"
                                            options={projects}
                                            getOptionLabel={(option) => option.name}
                                            className={styles.projectTask}
                                            defaultValue={projects[0]}
                                            onChange={(e, data) => {
                                                data && changeProject(data.id);
                                            }}
                                            renderInput={(params) => (
                                                <TextField
                                                    {...params}
                                                    label="Project"
                                                    variant="outlined"
                                                />
                                            )}
                                        />
                                        <FormControl variant="outlined">
                                            <InputLabel id="label">Get Data By</InputLabel>
                                            <Select
                                                labelId="label"
                                                value={type}
                                                className={styles.subTask}
                                                onChange={(e: any) => typeHandler(e.target.value)}
                                                label="Get Data By"
                                            >
                                                <MenuItem value={1}>Sub Tasks</MenuItem>
                                                <MenuItem value={2}>Tasks</MenuItem>
                                            </Select>
                                        </FormControl>
                                    </>
                                )}
                            </div>
                            <div className={styles.generateDetail}>
                                <h1> MONTHLY PROGRESS REPORT </h1>
                                <div className={styles.detailLeft}>
                                    <div className={styles.reportBox}>
                                        <p>PROJECT:</p>
                                        <span> {data && data.name ? data.name : "NA"} </span>
                                    </div>
                                    <div className={styles.reportBox}>
                                        <p>Description:</p>
                                        <span>
                                            {data && data.Description ? data.Description : "NA"}
                                        </span>
                                    </div>
                                    <div className={styles.reportBox}>
                                        <p>Client:</p>
                                        <span> {data && data.Client ? data.Client : "NA"} </span>
                                    </div>
                                    <div className={styles.reportBox}>
                                        <p>Client Project Manager:</p>
                                        <span> {data && data.Manager ? data.Manager : "NA"} </span>
                                    </div>
                                </div>
                                <div className={styles.detailRight}>
                                    <div className={styles.reportBox}>
                                        <p>Report No:</p>
                                        <span>
                                            {" "}
                                            <input
                                                type="text"
                                                name=""
                                                id=""
                                                onChange={(e) => {
                                                    setReport(e.target.value);
                                                }}
                                                className={styles.reportInput}
                                            />{" "}
                                        </span>
                                    </div>
                                    <div className={styles.reportBox}>
                                        <p>Report Date:</p>
                                        <span>{dayjs(new Date()).format("DD MMM YYYY")}</span>
                                    </div>
                                </div>
                            </div>
                            <div className={styles.progressWrapper}>
                                <ul className={styles.header}>
                                    <li className={styles.first}>OVERALL PROGRESS STATUS</li>
                                    <li className={styles.second}>Weighting* %</li>
                                    <li className={styles.third}>Progress To Date </li>
                                    <li className={styles.four}>For Client </li>
                                </ul>
                                {data &&
                                    data.ParentTasks &&
                                    data.ParentTasks.map((res: any, index: number) => {
                                        return (
                                            <React.Fragment key={index}>
                                                <ul className={`${styles.statusData}`}>
                                                    <li className={styles.first}>{res.name}</li>
                                                    <li className={styles.second}>
                                                        {(!res.Tasks || res.Tasks.length === 0) &&
                                                            `${res.weight}%`}
                                                    </li>
                                                    <li className={styles.third}>
                                                        <div className={styles.headingBox}>
                                                            {index === 0 && (
                                                                <div className={styles.title}> Planned % </div>
                                                            )}
                                                            {(!res.Tasks || res.Tasks.length === 0) && (
                                                                <span> {res.plannedPercentComplete}</span>
                                                            )}
                                                        </div>
                                                    </li>
                                                    <li className={styles.four}>
                                                        <div className={styles.headingBox}>
                                                            {index === 0 && (
                                                                <div className={styles.title}> Actual % </div>
                                                            )}
                                                            {(!res.Tasks || res.Tasks.length === 0) && (
                                                                <span> {res.actualPercentComplete} </span>
                                                            )}
                                                        </div>
                                                    </li>
                                                    <li className={styles.five}>
                                                        <div className={styles.headingBox}>
                                                            {index === 0 && (
                                                                <div className={styles.title}> Actual % </div>
                                                            )}
                                                            {(!res.Tasks || res.Tasks.length === 0) && (
                                                                <span>
                                                                    <input
                                                                        onChange={(e) => {
                                                                            res.clientActual = e.target.value;
                                                                            setValue(res.clientActual);
                                                                        }}
                                                                        type="text"
                                                                        value={res.clientActual}
                                                                        name=""
                                                                        placeholder=""
                                                                        id=""
                                                                        className={styles.statusInput}
                                                                    />{" "}
                                                                </span>
                                                            )}
                                                        </div>
                                                    </li>
                                                </ul>

                                                {res.Tasks &&
                                                    res.Tasks.map((item: any, ind: number) => {
                                                        return (
                                                            <ul
                                                                key={ind}
                                                                className={`${styles.statusData} ${styles.seprator}`}
                                                            >
                                                                <li className={styles.first}>
                                                                    <span>{item.name}</span>
                                                                </li>
                                                                <li className={styles.second}>
                                                                    {item.weight}%
                                                                </li>
                                                                <li className={styles.third}>
                                                                    <div className={styles.headingBox}>
                                                                        <span> {item.plannedPercentComplete}</span>
                                                                    </div>
                                                                </li>
                                                                <li className={styles.four}>
                                                                    <div className={styles.headingBox}>
                                                                        <span> {item.actualPercentComplete}</span>
                                                                    </div>
                                                                </li>
                                                                <li className={styles.five}>
                                                                    <div className={styles.headingBox}>
                                                                        <span>
                                                                            <input
                                                                                type="text"
                                                                                name=""
                                                                                onChange={(e) => {
                                                                                    item.clientActual = e.target.value;
                                                                                    setValue(item.clientActual);
                                                                                }}
                                                                                value={item.clientActual}
                                                                                placeholder=""
                                                                                id=""
                                                                                className={styles.statusInput}
                                                                            />{" "}
                                                                        </span>
                                                                    </div>
                                                                </li>
                                                            </ul>
                                                        );
                                                    })}
                                            </React.Fragment>
                                        );
                                    })}
                                {data && <ul className={`${styles.statusData} ${styles.totalData}   `}>
                                    <li className={styles.first}>Total </li>
                                    <li className={styles.second}>{data && data.totalweight}%</li>
                                    <li className={styles.third}>
                                        <div className={styles.headingBox}>
                                            <span> {data && data.planned}%</span>
                                        </div>
                                    </li>
                                    <li className={styles.four}>
                                        <div className={styles.headingBox}>
                                            <span> {data && data.actual}% </span>
                                        </div>
                                    </li>
                                    <li className={styles.five}>
                                        <div className={styles.headingBox}>
                                            <span>
                                                {" "}
                                                <input
                                                    type="text"
                                                    name=""
                                                    placeholder=""
                                                    id=""
                                                    onChange={(e) => {
                                                        data.actualClient = e.target.value;
                                                        setValue(data.actualClient);
                                                    }}
                                                    value={data.actualClient}
                                                    className={styles.statusInput}
                                                />{" "}
                                            </span>
                                        </div>
                                    </li>
                                </ul>}

                                <div className={styles.progressContent}>
                                    <h3> Work Completed to-date &amp; Work In Progress: </h3>
                                    <FormControl variant={"outlined"} className={styles.SelectTask} >
                                        <InputLabel>Select Tasks</InputLabel>
                                        <Select
                                            multiple
                                            value={workPlan}
                                            onChange={handleChange}
                                            renderValue={(selected) => (selected as string[]).join(', ')}
                                            MenuProps={MenuProps}
                                            label={"Select Tasks"}
                                        >
                                            {data && data.ParentTasks && data.ParentTasks.map((res: any, index: number) => (
                                                <MenuItem key={index} value={res.name}>
                                                    <Checkbox checked={workPlan.indexOf(res.name) > -1} />
                                                    <ListItemText primary={res.name} />
                                                </MenuItem>
                                            ))}
                                        </Select>
                                    </FormControl>
                                    <br />
                                    {
                                        workPlan.map((res: string, index: number) => <p>{index + 1}. {res}</p>)
                                    }
                                </div>
                                <div className={styles.progressContent}>
                                    <h3> Work Planned for Next Period: </h3>
                                    <FormControl variant={"outlined"} className={styles.SelectTask}>
                                        <InputLabel>Select Tasks</InputLabel>
                                        <Select
                                            multiple
                                            value={newPlanReport}
                                            onChange={handleChange2}
                                            renderValue={(selected) => (selected as string[]).join(', ')}
                                            MenuProps={MenuProps}
                                            label={"Select Tasks"}
                                        >
                                            {data && data.ParentTasks && data.ParentTasks.map((res: any, index: number) => (
                                                <MenuItem key={index} value={res.name}>
                                                    <Checkbox checked={newPlanReport.indexOf(res.name) > -1} />
                                                    <ListItemText primary={res.name} />
                                                </MenuItem>
                                            ))}
                                        </Select>
                                    </FormControl>
                                    <br />
                                    {
                                        newPlanReport.map((res: string, index: number) => <p>{index + 1}. {res}</p>)
                                    }
                                </div>
                                <div className={styles.progressDescBox}>
                                    {dilverables()}
                                    <ul className={styles.second}>
                                        <h3> Expected: </h3>
                                        {
                                            data &&
                                            data.tasks &&
                                            data.tasks.map((res: any, index: number) =>
                                                <li>{res.plannedFinish}</li>

                                            )
                                        }
                                    </ul>
                                    <ul className={styles.second}>
                                        <h3> Executed: </h3>
                                        {
                                            data &&
                                            data.tasks &&
                                            data.tasks.map((res: any, index: number) =>
                                                <li>{res.actualPercentComplete === 100 ? res.actualFinish : <span>&nbsp;</span>}</li>

                                            )
                                        }
                                    </ul>
                                </div>
                                {commentBoxHtml(0, commentBox1, "Pending Clarification / Decision Await From Customer")}
                                <div className={`${styles.progressDescBox} ${styles.scheduleBox}`}>
                                    <h2> Invoice Schedule </h2>
                                    <ul className={styles.first}>
                                        <h3> # </h3>
                                        {
                                            data &&
                                            data.tasks &&
                                            data.tasks.map((res: any, index: number) =>
                                                <li>MS{index + 1}</li>

                                            )
                                        }
                                    </ul>
                                    <ul className={styles.second}>
                                        <h3> Milestone: </h3>
                                        {
                                            data &&
                                            data.tasks &&
                                            data.tasks.map((res: any, index: number) =>
                                                <li>{res.name}</li>

                                            )
                                        }
                                    </ul>
                                    <ul className={styles.third}>
                                        <h3> % </h3>
                                        {
                                            data &&
                                            data.tasks &&
                                            data.tasks.map((res: any, index: number) =>
                                                <li>0%</li>

                                            )
                                        }
                                    </ul>
                                    <ul className={styles.four}>
                                        <h3> Expected: </h3>
                                        {
                                            data &&
                                            data.tasks &&
                                            data.tasks.map((res: any, index: number) =>
                                                <li>{res.plannedFinish}</li>

                                            )
                                        }
                                    </ul>
                                    <ul className={styles.five}>
                                        <h3> Executed: </h3>
                                        {
                                            data &&
                                            data.tasks &&
                                            data.tasks.map((res: any, index: number) =>
                                                <li>{res.actualPercentComplete === 100 ? res.actualFinish : <span>&nbsp;</span>}</li>

                                            )
                                        }
                                    </ul>
                                </div>

                                {commentBoxHtml(1, commentBox2, "Status Out of The Scope/ Change Order")}
                                {commentBoxHtml(2, commentBox3, "Areas of Concern, Tech. Queries List, NCR:")}
                                {commentBoxHtml(3, commentBox4, "Other Comments:")}

                                <br />
                                <div className={styles.generatewrapper}>
                                    <button onClick={handleExportWithComponent}> Generate report  </button>
                                </div>

                                {idDownloading && (
                                    <PDFExport ref={pdfExportComponent} paperSize="A2" fileName={`Report for ${new Date().getTime()}`}>
                                        <div style={{ padding: "15px" }}>
                                            <div className={styles.generateDetail}>
                                                <h1> MONTHLY PROGRESS REPORT </h1>
                                                <div className={styles.detailLeft}>
                                                    <div className={styles.reportBox}>
                                                        <p>PROJECT:</p>
                                                        <span> {data && data.name ? data.name : "NA"} </span>
                                                    </div>
                                                    <div className={styles.reportBox}>
                                                        <p>Description:</p>
                                                        <span>
                                                            {data && data.Description ? data.Description : "NA"}
                                                        </span>
                                                    </div>
                                                    <div className={styles.reportBox}>
                                                        <p>Client:</p>
                                                        <span>
                                                            {" "}
                                                            {data && data.Client ? data.Client : "NA"}{" "}
                                                        </span>
                                                    </div>
                                                    <div className={styles.reportBox}>
                                                        <p>Client Project Manager:</p>
                                                        <span>
                                                            {" "}
                                                            {data && data.Manager ? data.Manager : "NA"}{" "}
                                                        </span>
                                                    </div>
                                                </div>
                                                <div className={styles.detailRight}>
                                                    <div className={styles.reportBox}>
                                                        <p>Report No:</p>
                                                        <span> {reportNo} </span>
                                                    </div>
                                                    <div className={styles.reportBox}>
                                                        <p>Report Date:</p>
                                                        <span>{dayjs(new Date()).format("DD MMM YYYY")}</span>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className={styles.progressWrapper}>
                                                <ul className={styles.header}>
                                                    <li className={styles.first}>
                                                        OVERALL PROGRESS STATUS
                                                    </li>
                                                    <li className={styles.second}>Weighting* %</li>
                                                    <li className={styles.third}>Progress To Date </li>
                                                </ul>
                                                {data &&
                                                    data.ParentTasks &&
                                                    data.ParentTasks.map((res: any, index: number) => {
                                                        return (
                                                            <React.Fragment key={index}>
                                                                <ul className={styles.statusData}>
                                                                    <li className={styles.first}>{res.name}</li>
                                                                    <li className={styles.second}>
                                                                        {(!res.Tasks || res.Tasks.length === 0) &&
                                                                            `${res.weight}%`}
                                                                    </li>
                                                                    <li className={styles.third}>
                                                                        <div className={styles.headingBox}>
                                                                            {index === 0 && (
                                                                                <div className={styles.title}>
                                                                                    {" "}
                                                                                    Planned %{" "}
                                                                                </div>
                                                                            )}
                                                                            {(!res.Tasks || res.Tasks.length === 0) && (
                                                                                <span> {res.plannedPercentComplete}</span>
                                                                            )}
                                                                        </div>
                                                                    </li>
                                                                    <li className={styles.four}>
                                                                        <div className={styles.headingBox}>
                                                                            {index === 0 && (
                                                                                <div className={styles.title}>
                                                                                    {" "}
                                                                                    Actual %{" "}
                                                                                </div>
                                                                            )}
                                                                            {(!res.Tasks || res.Tasks.length === 0) && (
                                                                                <span> {res.clientActual} </span>
                                                                            )}
                                                                        </div>
                                                                    </li>
                                                                </ul>

                                                                {res.Tasks &&
                                                                    res.Tasks.map((item: any, ind: number) => {
                                                                        return (
                                                                            <ul
                                                                                key={ind}
                                                                                className={`${styles.statusData} ${styles.seprator}`}
                                                                            >
                                                                                <li className={styles.first}>
                                                                                    {item.name}
                                                                                </li>
                                                                                <li className={styles.second}>
                                                                                    {item.weight}%
                                                                                </li>
                                                                                <li className={styles.third}>
                                                                                    <div className={styles.headingBox}>
                                                                                        <span>
                                                                                            {" "}
                                                                                            {item.plannedPercentComplete}
                                                                                        </span>
                                                                                    </div>
                                                                                </li>
                                                                                <li className={styles.four}>
                                                                                    <div className={styles.headingBox}>
                                                                                        <span> {item.clientActual}</span>
                                                                                    </div>
                                                                                </li>
                                                                            </ul>
                                                                        );
                                                                    })}
                                                            </React.Fragment>
                                                        );
                                                    })}
                                                <ul
                                                    className={`${styles.statusData} ${styles.totalData} `}
                                                >
                                                    <li className={styles.first}>Total </li>
                                                    <li className={styles.second}>
                                                        {data && data.totalweight}%
                                                    </li>
                                                    <li className={styles.third}>
                                                        <div className={styles.headingBox}>
                                                            <span> {data && data.planned}%</span>
                                                        </div>
                                                    </li>
                                                    <li className={styles.four}>
                                                        <div className={styles.headingBox}>
                                                            <span> {data && data.actualClient}% </span>
                                                        </div>
                                                    </li>
                                                </ul>
                                            </div>
                                            {workPlan.length > 0 &&
                                                <div className={styles.progressContent}>
                                                    <h3> Work Completed to-date &amp; Work In Progress: </h3>
                                                    <br />
                                                    {
                                                        workPlan.map((res: string, index: number) => <p>{index + 1}. {res}</p>)
                                                    }
                                                </div>
                                            }
                                            {newPlanReport.length > 0 && <div className={styles.progressContent}>
                                                <h3> Work Planned for Next Period: </h3>
                                                <br />
                                                {
                                                    newPlanReport.map((res: string, index: number) => <p>{index + 1}. {res}</p>)
                                                }
                                            </div>
                                            }
                                            {data &&
                                                data.tasks && data.tasks.length > 0 && <div className={styles.progressDescBox}>
                                                    {dilverables()}
                                                    <ul className={styles.second}>
                                                        <h3> Expected: </h3>
                                                        {
                                                            data &&
                                                            data.tasks &&
                                                            data.tasks.map((res: any, index: number) =>
                                                                <li>{res.plannedFinish}</li>

                                                            )
                                                        }
                                                    </ul>
                                                    <ul className={styles.second}>
                                                        <h3> Executed: </h3>
                                                        {
                                                            data &&
                                                            data.tasks &&
                                                            data.tasks.map((res: any, index: number) =>
                                                                <li>{res.actualPercentComplete === 100 ? res.actualFinish : <span>&nbsp;</span>}</li>

                                                            )
                                                        }
                                                    </ul>
                                                </div>
                                            }
                                            {
                                            commentBoxes[0].length > 0 &&
                                                <div className={`${styles.progressDescBox} ${styles.customerBox} ${styles.commentwrapper}`}>
                                                    <ul className={styles.selectDataList}>
                                                        {
                                                            commentBoxes[0]?.map((res, ind) =>
                                                                <li>
                                                                    <span className={styles.selectContent}>{ind + 1}. {res.text}</span>
                                                                    <div className={styles.selectColorBox}>
                                                                        <span className={res.color === "green" ? styles.BackGreen : styles.BackRed}></span>
                                                                    </div>
                                                                </li>
                                                            )
                                                        }
                                                    </ul>
                                                </div>
                                            }
                                            {
                                                data &&
                                                data.tasks &&
                                                data.tasks.length > 0 &&

                                                <div className={`${styles.progressDescBox} ${styles.scheduleBox}`}>
                                                    <h2> Invoice Schedule </h2>
                                                    <ul className={styles.first}>
                                                        <h3> # </h3>
                                                        {
                                                            data &&
                                                            data.tasks &&
                                                            data.tasks.map((res: any, index: number) =>
                                                                <li>MS{index + 1}</li>

                                                            )
                                                        }
                                                    </ul>
                                                    <ul className={styles.second}>
                                                        <h3> Milestone: </h3>
                                                        {
                                                            data &&
                                                            data.tasks &&
                                                            data.tasks.map((res: any, index: number) =>
                                                                <li>{res.name}</li>

                                                            )
                                                        }
                                                    </ul>
                                                    <ul className={styles.third}>
                                                        <h3> % </h3>
                                                        {
                                                            data &&
                                                            data.tasks &&
                                                            data.tasks.map((res: any, index: number) =>
                                                                <li>0%</li>

                                                            )
                                                        }
                                                    </ul>
                                                    <ul className={styles.four}>
                                                        <h3> Expected: </h3>
                                                        {
                                                            data &&
                                                            data.tasks &&
                                                            data.tasks.map((res: any, index: number) =>
                                                                <li>{res.plannedFinish}</li>

                                                            )
                                                        }
                                                    </ul>
                                                    <ul className={styles.five}>
                                                        <h3> Executed: </h3>
                                                        {
                                                            data &&
                                                            data.tasks &&
                                                            data.tasks.map((res: any, index: number) =>
                                                                <li>{res.actualPercentComplete === 100 ? res.actualFinish : <span>&nbsp;</span>}</li>

                                                            )
                                                        }
                                                    </ul>
                                                </div>
                                            }
                                            {commentBoxes[1].length > 0 && <div className={`${styles.progressDescBox} ${styles.customerBox} ${styles.commentwrapper}`}>
                                                <ul className={styles.selectDataList}>
                                                    {
                                                        commentBoxes[1]?.map((res, ind) =>
                                                            <li>
                                                                <span className={styles.selectContent}>{ind + 1}. {res.text}</span>
                                                                <div className={styles.selectColorBox}>
                                                                    <span className={res.color === "green" ? styles.BackGreen : styles.BackRed}></span>
                                                                </div>
                                                            </li>
                                                        )
                                                    }
                                                </ul>
                                            </div>}
                                            {commentBoxes[2].length > 0 && <div className={`${styles.progressDescBox} ${styles.customerBox} ${styles.commentwrapper}`}>
                                                <ul className={styles.selectDataList}>
                                                    {
                                                        commentBoxes[2]?.map((res, ind) =>
                                                            <li>
                                                                <span className={styles.selectContent}>{ind + 1}. {res.text}</span>
                                                                <div className={styles.selectColorBox}>
                                                                    <span className={res.color === "green" ? styles.BackGreen : styles.BackRed}></span>
                                                                </div>
                                                            </li>
                                                        )
                                                    }
                                                </ul>
                                            </div>}
                                            {commentBoxes[3].length > 0 && <div className={`${styles.progressDescBox} ${styles.customerBox} ${styles.commentwrapper}`}>
                                                <ul className={styles.selectDataList}>
                                                    {
                                                        commentBoxes[3]?.map((res, ind) =>
                                                            <li>
                                                                <span className={styles.selectContent}>{ind + 1}. {res.text}</span>
                                                                <div className={styles.selectColorBox}>
                                                                    <span className={res.color === "green" ? styles.BackGreen : styles.BackRed}></span>
                                                                </div>
                                                            </li>
                                                        )
                                                    }
                                                </ul>
                                            </div>}
                                        </div>
                                    </PDFExport>
                                )}
                            </div>
                        </div>
                    </div>
                </div>
            </PageContainer>
        </>
    );
};

export default Genratereport;
