import { apiCall } from "../api";
import { toast } from 'react-toastify';
import { constant } from '../constant/constant'
import StorageUtil from '../../../../utils/StorageUtil';

const getRoleString = (role) => { return (role===1?"Technician":(role===2?"Project Manager":"Administrator")); }

export const login = (data) => async () => {
  try {
    const result = await apiCall({
      url: `${process.env.REACT_APP_API_URL}api/v1/auth/login`,
      method: "POST",
      payload: data,
    });

    StorageUtil.setLocalStorage('id', result.data.data.id);
    StorageUtil.setLocalStorage('token', result.data.token);
    StorageUtil.setLocalStorage('name', result.data.data.name);
    StorageUtil.setLocalStorage('email', result.data.data.email);
    StorageUtil.setLocalStorage('role', result.data.data.role);
    StorageUtil.setLocalStorage('isLoggedIn', true);

    toast.success("Logged in successfully", { autoClose: 2000 })

    return result;
  } catch (error) {
    console.log(error);
    toast.error(error.response.data.message, { autoClose: 2000 });
    return error;
  }
};

export const signup = (data) => async () => {
  try {
    const result = await apiCall({
      url: `${process.env.REACT_APP_API_URL}api/v1/auth/register`,
      method: "POST",
      payload: data,
    });
    return result;
  } catch (error) {
    toast.error(error.response.data.message, { autoClose: 2000 });
    return error
  }
};

export const emailVerify = (token) => async () => {
  try {
    await apiCall({
      url: `${process.env.REACT_APP_API_URL}api/v1/auth/email/verify?token=${token}`,
      method: "GET",
    });
    toast.success("Email verified successfully.", { autoClose: 2000 });
  } catch (error) {
    toast.error(error.response.data.message, { autoClose: 2000 });
    throw error.response.data;
  }
};

export const getAllUser = () => async(dispatch) => {
  try {
    const result = await apiCall({
      url: `${process.env.REACT_APP_API_URL}api/v1/users`,
      method: "GET",
    });
    let payload = [];
    result.data.body.forEach(user => {
      payload.push({
        id: user.id, 
        name: user.name, 
        email: user.email, 
        role: user.role, 
        createdAt: user.createdAt,
        updatedAt: user.updatedAt,
        roleString: getRoleString(user.role)
      });
    });
    console.log("payload",payload)
    dispatch({ type: constant.GET_ALL_USER, payload });
    return result;
  } catch (error) {
    toast.error(error.response.data.message, { autoClose: 2000 });
    return error
  }
};

export const createUser = (data) => async (dispatch) => {
  try {
    const result = await apiCall({
      url: `${process.env.REACT_APP_API_URL}api/v1/users`,
      method: "POST",
      payload: data,
    });
    result.data.body.roleString = getRoleString(result.data.body.role);
    dispatch({ type: constant.CREATE_USER, payload: result.data.body });
    return true;
  } catch (error) {
    toast.error(error.response.data.message, { autoClose: 2000 });
    return false;
  }
};

export const updateUser = (id, data) => async (dispatch) => {
  try {    
    const result = await apiCall({
      url: `${process.env.REACT_APP_API_URL}api/v1/users/${id}`,
      method: "PUT",
      payload: data,
    });
    result.data.body.roleString = getRoleString(result.data.body.role);
    dispatch({ type: constant.UPDATE_USER, payload: result.data.body });
    return true;
  } catch (error) {
    toast.error(error.response.data.message, { autoClose: 2000 });
    return false;
  }
};

