import { apiCall } from "../api";
import { toast } from 'react-toastify';
import { constant } from '../constant/constant'

export const fetchProjectsView = () => async (dispatch) => {
  try {
    const result = await apiCall({
      url: `${process.env.REACT_APP_API_URL}api/v1/projects/view`,
      method: "GET",
    });
    dispatch({ type: constant.GET_PROJECT_VIEW, payload: result.data.body });
  } catch (error) {
    toast.error(error.response.data.message, { autoClose: 2000 });
    return error
  }
}