import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from "react-redux";
import { toast, ToastContainer } from 'react-toastify';
import dayjs from "dayjs";
import StorageUtil from '../../utils/StorageUtil';
import PageContainer from '../../common/components/pagecontainer/PageContainer';
import TimeEntry from './TimeEntry';
import DatePickerCard from '../../common/lib/DatePicker/DatePicker'

import { getTasks, getUserTaskByUserId, getTaskList, updateTask, getProjects, getAssignedProject } from '../../common/core/store/common/commonAction'
import { assignedProjects } from '../../common/core/store/common/commonSelector'
import styles from './Home.module.scss';
import Loader from '../../../src/assets/images/loader.svg'

const Home = () => {
    const dispatch = useDispatch();
    const userProjects = useSelector(assignedProjects);
    const [dates, setDates] = useState(null);
    const [tasks, setTasks] = useState([]);
    const [userTasks, setUserTasks] = useState(null)
    const [taskList, setTaskList] = useState([]);
    const [unAssigned, setUnAssignedTasks] = useState([])
    const [project, setProjects] = useState(null);
    const [lastUpdated, setLastUpdated] = useState(null);
    const [isUpdating, setUpdate] = useState(false);
    const [remvoeAr, setRemoveAr] = useState([]);
    const [disableProject, setDisableProject] = useState(false);
    // const [disabledVal, setDisabledVal] = useState(false);

    // useEffect(() => {
    //     if (dates) {
    //         let startDate = new Date(dates.startDate);
    //         const date = new Date();
    //         const firstDay = new Date(date.getFullYear(), date.getMonth(), 1);
    //         const PrevMonthfirstDay = new Date(date.getFullYear(), date.getMonth() - 1, 1);
    //         if ((new Date().getDate() > 10 && startDate < firstDay) || startDate < PrevMonthfirstDay)
    //             if (new Date(dates.endDate).getMonth() !== new Date().getMonth())
    //                 setDisabledVal(true);
    //             else
    //                 setDisabledVal(false);
    //         else
    //             setDisabledVal(false);
    //     }
    // }, [dates])

    const resetFunction = () => {
        if (dates)
            getData(dates.startDate, dates.endDate, userTasks, taskList);
    }

    useEffect(() => {
        try {
            if (StorageUtil.getLocalStorage('id')) {
                getUserTaskList(StorageUtil.getLocalStorage('id'));
            }
        } catch (error) {
            console.log(error);
        }
    }, [dates])

    useEffect(() => {
        resetFunction();
    }, []);

    useEffect(() => {
        try {
            if (StorageUtil.getLocalStorage('id')) {
                dispatch(getAssignedProject(StorageUtil.getLocalStorage('id')))
            }
        } catch (error) {
            console.log(error);
        };
    }, [dispatch, StorageUtil.getLocalStorage('id')])

    const getTasksList = (userTaskList) => {
        dispatch(getTaskList()).then(res => {
            setTaskList([...res.data.body])
            if (dates)
                getData(dates.startDate, dates.endDate, userTaskList, [...res.data.body]);
        })
    }

    const getUserTaskList = () => {
        dispatch(getUserTaskByUserId(StorageUtil.getLocalStorage('id'))).then(res => {
            setUserTasks([...res.data.body]);
            getTasksList([...res.data.body]);
        })
    }

    const getDayCount = (i) => {
        let count = 0;
        let json = [...tasks, ...unAssigned];
        for (let index = 0; index < json.length; index++) {
            count += json[index].timesheets && json[index].timesheets[i] && parseFloat(json[index].timesheets[i].timespent)
                && json[index].timesheets[i].timespent !== '0.0'
                && json[index].timesheets[i].timespent !== '0'
                ? parseFloat(json[index].timesheets[i].timespent) : 0;
        }
        if (count % 1 !== 0)
            count = count.toFixed(2);

        return count;
    }

    const getData = (startDate, endDate, userTasksList, taskLists) => {
        let disableButton = false;
        setUpdate(true);
        dispatch(getTasks({ startDate: dayjs(startDate).format('YYYY-MM-DD'), endDate: dayjs(endDate).format('YYYY-MM-DD') })).then(res => {
            const assigned = res.assigned;
            const unAssign = res.unAssigned;
            res.timesheetLastUpdated && setLastUpdated(res.timesheetLastUpdated);
            if (assigned.length > 0) {
                assigned.map(el => {
                    let taskListData = userTasksList.filter(todo => todo.projectId === el.projectId);
                    if (el['timesheets'] &&
                        !el['timesheets'][el['timesheets'].length - 1]['isEdit']
                        && !disableButton) {
                        disableButton = true;
                    }
                    el['task'] = taskListData;
                    return el;
                })
                setTasks([...assigned]);
            }
            if (unAssign.length > 0) {
                unAssign.map(el => {
                    if (el.projectId) {
                        let taskListData = taskLists.filter(todo => todo.projectId === el.projectId);
                        el['task'] = taskListData;
                        return el;
                    }
                })
                setUnAssignedTasks([...unAssign]);
            }

            setDisableProject(disableButton);
            tasks && count();
            setUpdate(false);
        });
        dispatch(getProjects()).then(res => {
            res.data.body.sort(compare);
            res.data && setProjects(res.data.body)
        })
    }

    const compare = (a, b) => {
        if (a.name < b.name) {
            return -1;
        }
        if (a.name > b.name) {
            return 1;
        }
        return 0;
    }

    const onDateChange = (startDate, endDate) => {
        setDates({ startDate, endDate });
        setUpdate(true);
        //    resetFunction();
    }

    const changeProject = (type, val, taskId) => {
        if (type === 1) {
            unAssigned[taskId].projectId = val;
            setUnAssignedTasks([...unAssigned])
            let taskListData = taskList.filter(todo => todo.projectId === parseInt(val));
            const taskListDataSort = Object.values(taskListData.reduce((r, c) => {
                r[c.name] = c
                return r
            }, {}));
            unAssigned[taskId].projectError = false;
            taskListDataSort.sort(compare);
            console.log(taskListDataSort);
            unAssigned[taskId].task = taskListDataSort;
            setUnAssignedTasks([...unAssigned])
        } else {
            tasks[taskId].projectId = val;
            setTasks([...tasks]);
            let taskListData = userTasks.filter(todo => todo.projectId === parseInt(val));
            const taskListDataSort = Object.values(taskListData.reduce((r, c) => {
                r[c.name] = c
                return r
            }, {}));
            taskListDataSort.sort(compare);
            tasks[taskId].task = taskListDataSort;
            tasks[taskId].projectError = false;
            setTasks([...tasks]);
        }
    }

    const changeTask = (type, val, taskId) => {
        const value = val.split('~~');
        const InputType = value[0] === 'task' ? 0 : 1;
        if (type === 1) {
            unAssigned[taskId].taskError = false;
            unAssigned[taskId].taskId = InputType === 1 ? value[1] : 0;
            unAssigned[taskId].parentTaskId = InputType === 0 ? value[1] : 0;
            setUnAssignedTasks([...unAssigned])
        } else {
            tasks[taskId].taskError = false;
            tasks[taskId].taskId = InputType === 1 ? value[1] : 0;
            tasks[taskId].parentTaskId = InputType === 0 ? value[1] : 0;
            setTasks([...tasks]);
        }
        return;
    }

    const arrayPusher = (length, rowId, taskId, value, type) => {
        let ar = {};
        for (let i = length; i <= rowId; i++) {
            let val = '';
            if (i === Number(rowId))
                val = value
            let startDate = new Date(dates.startDate);
            startDate.setDate(startDate.getDate() + i);
            ar = {
                date: dayjs(startDate).format('YYYY-MM-DD'),
                timespent: val,
            }
            if (type === 1) {
                if (!unAssigned[taskId]['timesheets'])
                    unAssigned[taskId]['timesheets'] = [];
                unAssigned[taskId]['timesheets'].push(ar);
            } else {
                if (!tasks[taskId]['timesheets'])
                    tasks[taskId]['timesheets'] = [];
                tasks[taskId]['timesheets'].push(ar);
            }
        }
        if (type === 1)
            setUnAssignedTasks([...unAssigned]);
        else
            setTasks([...tasks]);
    }

    const blurHandler = (e, type) => {
        e.preventDefault();
        const taskId = e.target.getAttribute('taskId');
        const rowId = e.target.getAttribute('rowid');
        let sheetError = false;
        if (getDayCount(rowId) > 24) {
            toast.warn("Maximum time entry allowed per day is 24 hours.", { autoClose: 2000 })
            sheetError = true;
        } else
            sheetError = false;
        for (let i = 0; i < tasks.length; i++) {
            if (tasks[i].timesheets && tasks[i].timesheets[rowId])
                tasks[i].timesheets[rowId].sheetError = sheetError
        }
        for (let i = 0; i < unAssigned.length; i++) {
            if (unAssigned[i].timesheets && unAssigned[i].timesheets[rowId])
                unAssigned[i].timesheets[rowId].sheetError = sheetError
        }
        if (e.target.value) {
            const parsedVal = parseFloat(e.target.value) % 1
            const validNumber = new RegExp(/^\d*\.?\d*$/);
            if (!validNumber.test(e.target.value)) {
                toast.warn("Invalid time input. Accepted numbers only for example 0.5, 1 or 1.5", { autoClose: 2000 })
                if (type === 1)
                    unAssigned[taskId]['timesheets'][rowId]['sheetError'] = true
                else
                    tasks[taskId]['timesheets'][rowId]['sheetError'] = true
            }
            else if (parsedVal !== 0 && parsedVal !== 0.5) {
                toast.warn("Please input hours in multiple of 0.5 For example 0.5 ,1 or 1.5", { autoClose: 2000 })
                if (type === 1)
                    unAssigned[taskId]['timesheets'][rowId]['sheetError'] = true
                else
                    tasks[taskId]['timesheets'][rowId]['sheetError'] = true
            }
        }
        setTasks([...tasks]);
        setUnAssignedTasks([...unAssigned]);
    }


    const chnageHandler = (e, type) => {
        e.preventDefault();
        const taskId = e.target.getAttribute('taskId');
        const rowId = e.target.getAttribute('rowid');
        if (e.target.value === '.')
            e.target.value = '0.';
        if (type === 1) {
            const length = unAssigned[taskId]['timesheets'] ? unAssigned[taskId]['timesheets'].length : 0;
            if (length >= rowId && unAssigned[taskId]['timesheets'] && unAssigned[taskId]['timesheets'][rowId]) {
                unAssigned[taskId]['timesheets'][rowId]['timespent'] = (e.target.value);
                setUnAssignedTasks([...unAssigned]);
            } else
                arrayPusher(length, rowId, taskId, e.target.value, type);
        } else {
            const length = tasks[taskId]['timesheets'] ? tasks[taskId]['timesheets'].length : 0;
            if (length >= rowId && tasks[taskId]['timesheets'] && tasks[taskId]['timesheets'][rowId]) {
                tasks[taskId]['timesheets'][rowId]['timespent'] = e.target.value
                setTasks([...tasks]);
            } else
                arrayPusher(length, rowId, taskId, e.target.value);
        }
    }

    const updateHandler = () => {
        let snapCount = document.getElementsByClassName(styles.errorBorder).length;
        if (snapCount === 0) {
            let timesheet = [];
            let check = 0;
            tasks.forEach((element, row) => {
                let total = 0;
                element.timesheets && element.timesheets.forEach(el => {
                    if (el.timespent !== '0' && el.timespent !== '0.0' && el.timespent !== '') {
                        timesheet.push({ parentTaskId: element.parentTaskId, isDeleted: false, projectId: element.projectId, taskId: element.taskId ? element.taskId : element.id ? element.id : 0, date: el.date, timespent: el.timespent, isAssigned: true })
                        total += parseFloat(el.timespent);
                    }
                })
                if (total !== 0 && element.timesheets && element.timesheets.length > 0 && (!element.projectId || !element.taskId)) {
                    if (!element.projectId) {
                        check++;
                        tasks[row].projectError = true;
                    }
                    if ((!element.taskId && !element.parentTaskId) && !element.id) {
                        tasks[row].taskError = true;
                        check++;
                    }
                    setTasks([...tasks]);
                    return false;
                } else {
                    tasks[row].projectError = false;
                    tasks[row].taskError = false;
                    setTasks([...tasks]);
                }
            });
            unAssigned.forEach((element, row) => {
                let total = 0;
                element.timesheets && element.timesheets.forEach(el => {
                    if (el.timespent !== '0' && el.timespent !== '0.0' && el.timespent !== '') {
                        timesheet.push({ parentTaskId: element.parentTaskId, isDeleted: false, projectId: parseInt(element.projectId), taskId: element.taskId ? parseInt(element.taskId) : element.id ? parseInt(element.id) : 0, date: el.date, timespent: parseFloat(el.timespent), isAssigned: false })
                        total += parseFloat(el.timespent);
                    }
                })
                if (total !== 0 && element.timesheets && element.timesheets.length > 0 && (!element.projectId || !element.taskId)) {
                    if (!element.projectId) {
                        unAssigned[row].projectError = true;
                        check++;
                    }
                    if ((!element.taskId && !element.parentTaskId) && !element.id) {
                        unAssigned[row].taskError = true;
                        check++;
                    }
                    setUnAssignedTasks([...unAssigned]);
                    return false;
                } else {
                    unAssigned[row].projectError = false;
                    unAssigned[row].taskError = false;
                    setUnAssignedTasks([...unAssigned]);
                }
            });
            timesheet = timesheet.concat(remvoeAr);
            if (check > 0)
                toast.warn('Project and Task are mandatory fields. Please select both.', { autoClose: 2000 });
            if (timesheet.length === 0)
                toast.warn('Please enter hours to update timesheet.', { autoClose: 2000 });
            setTimeout(() => {
                snapCount = document.getElementsByClassName(styles.errorBorder).length;
                if (snapCount === 0 && timesheet.length > 0) {
                    setUpdate(true);
                    try {
                        dispatch(updateTask({ timesheet: timesheet })).then(res => {
                            if (res.status && res.status === 201) {
                                toast.success("Data Updated successfully", { autoClose: 2000 });
                                resetFunction();
                                setRemoveAr([]);
                            }
                        });
                    } catch (error) {
                        setUpdate(false);
                        console.log(error)
                    }
                }
            }, 100)
        }
    }

    const count = () => {
        let task = [];
        let unAssigne = [];
        if (tasks) {
            task = tasks;
        }
        if (unAssigned) {
            unAssigne = unAssigned;
        }

        let json = task.concat(unAssigne);
        let count = 0;
        for (let index = 0; index < json.length; index++) {
            if (json[index] && json[index].timesheets) {
                for (let i = 0; i < json[index].timesheets.length; i++) {
                    if (json[index].timesheets[i] && json[index].timesheets[i].timespent !== '')
                        count += parseFloat(json[index].timesheets[i].timespent) ? parseFloat(json[index].timesheets[i].timespent) : 0;
                }
            }
        }
        if (count % 1 !== 0)
            count = count.toFixed(2);
        return count;
    }

    const addRowFunction = (type) => {
        if (type === 1) {
            unAssigned.push({});
            setUnAssignedTasks([...unAssigned]);
        }
        else {
            tasks.push({});
            setTasks([...tasks]);
        }
    }

    const removeRowFunction = (row, type, rowType) => {
        let removeAr = remvoeAr;
        if (type === 1) {
            unAssigned[row] && unAssigned[row].timesheets && unAssigned[row].timesheets.forEach(el => {
                if (el.timespent !== '')
                    removeAr.push({ isDeleted: true, projectId: parseInt(unAssigned[row].projectId), taskId: unAssigned[row].taskId ? parseInt(unAssigned[row].taskId) : unAssigned[row].id ? parseInt(unAssigned[row].id) : 0, date: el.date, timespent: parseFloat(el.timespent), isAssigned: false })
            })
            if (rowType === 0)
                unAssigned.splice(row, 1)
            else
                unAssigned[row] = { projectId: null }
            setUnAssignedTasks([...unAssigned]);
        } else {
            tasks[row] && tasks[row].timesheets && tasks[row].timesheets.forEach(el => {
                if (el.timespent !== '')
                    removeAr.push({ isDeleted: true, projectId: parseInt(tasks[row].projectId), taskId: tasks[row].taskId ? parseInt(tasks[row].taskId) : tasks[row].id ? parseInt(tasks[row].id) : 0, date: el.date, timespent: parseFloat(el.timespent), isAssigned: true })
            })
            if (rowType === 0)
                tasks.splice(row, 1);
            else
                tasks[row] = { projectId: null }
            setTasks([...tasks]);
        }
        setRemoveAr([...removeAr]);
    }

    return (
        <>
            {isUpdating && <div className={styles.loaderwrapper}>
                <div className={styles.loaderinner}>
                    <img alt="loader" src={Loader} />
                </div>
            </div>}
            <PageContainer>
                <div className={styles.timewrapper}>
                    <div className={styles.toptitlebox}>
                        <div className={styles.leftbox}>
                            <p> Your Profile </p>
                            <p> <strong>Time Entry </strong>  </p>
                        </div>
                        {lastUpdated &&
                            <div className={styles.rightbox}>
                                <p> Last update, {lastUpdated} </p>
                                <p> Reward!! </p>
                            </div>
                        }
                    </div>
                    <div className={styles.mainbox}>
                        <div className={styles.mainboxinner}>
                            <div className={styles.timeentrybox}>
                                <div className={styles.entryleft}>
                                    <p> Time Entry </p>
                                    {tasks && <DatePickerCard onDateChange={onDateChange} />}
                                </div>
                                <div className={styles.entryright}>
                                    <p> Get your rewards!! </p>
                                    <p> You need to update your time entry now </p>
                                </div>
                            </div>
                            <h2> Please input your hours </h2>
                            {tasks && <TimeEntry blurHandler={blurHandler} setTasks={setTasks}
                                getDayCount={getDayCount} unAssigned={unAssigned}
                                userProjects={userProjects}
                                removeRowFunction={removeRowFunction}
                                addRowFunction={addRowFunction}
                                dates={dates}
                                chnageHandler={chnageHandler}
                                changeTask={changeTask} project={project}
                                changeProject={changeProject}
                                disableProject={disableProject}
                                tasks={tasks} />
                            }
                        </div>
                        <div className={styles.messagebox}>
                            <div className={styles.totalweek}>
                                <span> Total Week </span>
                                <span>   {tasks && count()}  </span>
                            </div>
                            <div className={styles.infobox}>
                                <div className={styles.updatebox}>
                                    <p> Please, review and  {('\n')} update carefully.  </p>
                                    { }
                                    <button onClick={() => {
                                        !disableProject && !isUpdating && updateHandler()
                                    }} className={styles.themeButton}> Update </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <ToastContainer />
            </PageContainer>
        </>
    );
}

export default Home;
