export const getProjectReportByDate = (data) => async (dispatch) => {
  try {
    let url = "";
    if (data.type === 1) {
      url = `reportByUser/${data.startDate}/${data.endDate}/${data.userId}`;
    } else if (data.type === 2) {
      url = `yearlyProjectReport/${data.year}`
    } else if (data.type === 3) {
      url = `yearlyUserReport/${data.year}`
    }
    else if (data.type === 5) {
      url = `getProjectReportByManagerById/${data.startDate}/${data.endDate}/${data.id}`;
    }
    else if (data.type === 6) {
      url = `getProjectResourcesHours`;
    }
    else if (data.type === 7) {
      url = `getProjectTaskReport/${data.startDate}/${data.endDate}/${data.id}`;
    }
    else if (data.type === 8) {
      url = `getActiveProjectReport`;
    }
    else {
      url = `report/${data.startDate}/${data.endDate}`;
    }
    const result = await apiCall({
      url: `${process.env.REACT_APP_API_URL}api/v1/projects/${url}`,
      method: "GET",
    });
    dispatch({ type: constant.GET_PROJECT_REPORT_BY_DATE, payload: result.data.body });
  } catch (error) {
    console.log(error.response);
    //  toast.error(error.response.data.message);
    return error
  }
}

