import React, { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Dialog from "@mui/material/Dialog";
import styles from "./DownloadReport.module.scss";
import PageContainer from "../../common/components/pagecontainer/PageContainer";
import {
  getProjectReportByDate,
  resetProjectReport,
  getProjects,  
} from "../../common/core/store/common/commonAction";
import { getAllUser } from "../../common/core/store/users/usersAction";
import {
  getAllProjects,
  getProjectByReportByDate,
} from "../../common/core/store/common/commonSelector";
import { selectAllUser } from "../../common/core/store/users/usersSelector";
import DatePickerCard from "../../common/lib/DatePicker/DatePicker";
import { ToastContainer } from "react-toastify";
import moment from "moment";
import { CSVLink } from "react-csv";

const DownloadReports = () => {
  const csvRef: any = useRef();
  const dispatch = useDispatch();
  const reportData = useSelector(getProjectByReportByDate);
  const users = useSelector(selectAllUser)
  
  const [currentData, setCurrentData] = useState<any>({
    type: 0,
    startDate: new Date(),
    endDate: new Date(),
    id: 0,
    userId: 0,
    year: 2021
  });
  const [showDate, setShowDate] = React.useState(true);
  const [showProject, setShowProject] = React.useState(false);
  const [showUser, setShowUser] = React.useState(false)
  const [showYear, setShowYear] = React.useState(false);

  const downLoadHandler = () => {
    dispatch(getProjectReportByDate(currentData));
  };


  const changeHandler = (e: any) => {
    let value = e.target.value;
    if (e.target.name === "type") {
      value = parseInt(value);
      currentData[e.target.name] = value;
      if (value === 2 || value === 3 || value === 6 || value === 8) {
        setShowDate(false);
      } else {
        setShowDate(true);
      }
      if (value === 5 || value === 7) {
        setShowProject(true);
      } else {
        setShowProject(false);
      }
      if (value === 1) {
        setShowUser(true);
      } else {
        setShowUser(false);
      }

      if(value === 2 || value === 3)
      {
        setShowYear(true);
      }else{
        setShowYear(false);
      }
    } else {
      currentData[e.target.name] = value;
    }
    setCurrentData(currentData);
  };

  const projects = useSelector(getAllProjects);

  useEffect(() => {
    if (!projects) {
      dispatch(getProjects());
    }
    if (users.length === 0) {
      dispatch(getAllUser());
    }
  }, [dispatch, projects, users]);

  return (
    <>
      <PageContainer>
        <div className={styles.timewrapper}>
          <div className={styles.toptitlebox}>
            <div className={styles.leftbox}>
              <p> Download Report - CSV </p>
              <p>
                {" "}
                <strong>Setup Project </strong>{" "}
              </p>
            </div>
            <div className={styles.rightbox}></div>
          </div>
          <div className={styles.mainbox}>
            <div className={styles.mainboxinner}>
              <div className={styles.databox}>
                <span>Download Report - CSV</span>
                <div className={styles.inputbox}>
                  <p className={styles.label_custom}>Report Type *</p>
                  <div className={styles.select}>
                    <select
                      className={styles.input_custom}
                      name={"type"}
                      onChange={changeHandler}
                    >
                      <option value={0}>Project Reports</option>
                      <option value={1}>Project Report By User</option>
                      <option value={5}>Project Report By Manager</option>
                      <option value={6}>Workload Per Employee</option>
                      <option value={2}>Yearly Project Report</option>
                      <option value={3}>Yearly User Report</option>
                      <option value={7}>Project Task Report By User</option>
                      <option value={8}>Active Project Report</option>
                    </select>
                  </div>
                </div>
                {showDate && (
                  <>
                    <div className={styles.inputbox}>
                      <p className={styles.label_custom}>Date From *</p>
                      <DatePickerCard
                        onDateChange={(e: any) =>
                          changeHandler({
                            target: {
                              value: moment(e).format(),
                              name: "startDate",
                            },
                          })
                        }
                        isSingle={true}
                        defaultDate={currentData && currentData.startDate}
                      />
                    </div>
                    <div className={styles.inputbox}>
                      <p className={styles.label_custom}>Date To *</p>
                      <DatePickerCard
                        onDateChange={(e: any) =>
                          changeHandler({
                            target: {
                              value: moment(e).format(),
                              name: "endDate",
                            },
                          })
                        }
                        isSingle={true}
                        defaultDate={currentData && currentData.endDate}
                      />
                    </div>
                  </>
                )}
                {showProject && (
                  <div className={styles.inputbox}>
                    <p className={styles.label_custom}>Project *</p>
                    <div className={styles.select}>
                      <select
                        className={styles.input_custom}
                        name={"id"}
                        onChange={changeHandler}
                      >
                        <option>Select Project</option>
                        {projects &&
                          projects.map(
                            (
                              res: {
                                id:
                                | string
                                | number
                                | readonly string[]
                                | undefined;
                                name:
                                | boolean
                                | React.ReactChild
                                | React.ReactFragment
                                | React.ReactPortal
                                | null
                                | undefined;
                              },
                              index: number
                            ) => {
                              return (
                                <option key={index} value={res.id}>
                                  {res.name}
                                </option>
                              );
                            }
                          )}
                      </select>
                    </div>
                  </div>
                )}
                {
                  showUser && (

                  <div className={styles.inputbox}>
                    <p className={styles.label_custom}>User *</p>
                    <div className={styles.select}>
                      <select
                        className={styles.input_custom}
                        name={"userId"}
                        onChange={changeHandler}
                      >
                        <option>Select User</option>
                        {users &&
                          users.map(
                            (
                              res: {
                                id:
                                | string
                                | number
                                | readonly string[]
                                | undefined;
                                name:
                                | boolean
                                | React.ReactChild
                                | React.ReactFragment
                                | React.ReactPortal
                                | null
                                | undefined;
                              },
                              index: number
                            ) => {
                              return (
                                <option key={index} value={res.id}>
                                  {res.name}
                                </option>
                              );
                            }
                          )}
                      </select>
                    </div>
                  </div>
                  )}
                  {showYear && (
                  <div className={styles.inputbox}>
                    <p className={styles.label_custom}>Year *</p>
                    <div className={styles.select}>
                      <select
                        className={styles.input_custom}
                        name={"year"}
                        onChange={changeHandler}
                      >
                        <option>Select Year</option>
                        <option value="2017">2017</option>
                        <option value="2018">2018</option>
                        <option value="2019">2019</option>
                        <option value="2020">2020</option>
                        <option value="2021">2021</option>
                      </select>
                    </div>
                  </div>
                  )}
              </div>
            </div>
            <div className={styles.messagebox}>
              <div className={styles.infobox}>
                <div className={styles.updatebox}>
                  <button
                    onClick={downLoadHandler}
                    className={styles.themeButton}
                  >
                    {" "}
                    Preview Report{" "}
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
        {reportData && (
          <Dialog maxWidth={"xl"} open={true}>
            <div className={styles.cutomTableContainer}>
              <table>
                <thead>
                  <tr>
                    {reportData.field.map(
                      (res: { label: string; key: string }) => (
                        <th>{res.label}</th>
                      )
                    )}
                  </tr>
                </thead>
                <tbody>
                  {reportData.data.map((res: any) => (
                    <tr>
                      {reportData.field.map(
                        (item: { label: string; key: string }) => (
                          <td>{res[item.key]}</td>
                        )
                      )}
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
            <div className={styles.buttonContainer}>
              <span>
                <CSVLink
                  onClick={() => dispatch(resetProjectReport())}
                  filename={`${new Date().getTime()}.xls`}
                  ref={csvRef}
                  data={reportData && reportData.data}
                  headers={reportData && reportData.field}
                >
                  Download
                </CSVLink>
                <button
                  onClick={() => dispatch(resetProjectReport())}
                  className={styles.themeButton}
                >
                  {" "}
                  Close{" "}
                </button>
              </span>
            </div>
          </Dialog>
        )}
      </PageContainer>
      <ToastContainer />
    </>
  );
};

export default DownloadReports;
