import { constant } from '../constant/constant'

const initialState = {
  userTasks: null,
  assignedProjects: null,
  allProjects: null,
  graphData: null,
  apiStatus: false,
  projectReportByDate: null,
  settingDate: null,
  projectReportById: null,
  projectReport: null,
};

const commonReducer = (state = initialState, { type, payload } = {}) => {
  switch (type) {
    case constant.USER_TASKS: {
      return { ...state, userTasks: payload };
    }
    case constant.ASSIGNED_PROJECT: {
      return { ...state, assignedProjects: payload }
    }
    case constant.ALL_PROJECTS: {
      return { ...state, allProjects: payload }
    }
    case constant.GET_GRAPH_DATA: {
      return { ...state, graphData: payload }
    }
    case constant.GET_CELOXIS_API_CALL: {
      return { ...state, apiStatus: payload }
    }
    case constant.GET_PROJECT_REPORT_BY_DATE: {
      return { ...state, projectReportByDate: payload }
    }
    case constant.GET_DATE_SETTING: {
      return { ...state, settingDate: payload }
    }
    case constant.GET_PROJECT_REPORT_BY_ID: {
      return { ...state, projectReportById: payload }
    }
    case constant.GET_PROJECT_REPORT: {
      return { ...state, projectReport: payload }
    }
    default:
      return state;
  }
};

export default commonReducer;
